import DataSourceBrowserDB from "../../common/DataSourceBrowserDB";
import { Logger } from "../../logger";
import { ApexRemoteDataSource, ApexRemoteInput } from "../apexremote";
import { ApexRestDataSource, ApexRestInput } from "../apexrest";
import IDataSource from "../DataSource.interface";
import Delay from "../utils/delay";
export class DualDataSource implements IDataSource {
  public useRemote: boolean;
  private connection: any;
  private apexRestInput: ApexRestInput;
  private apexRemoteInput: ApexRemoteInput;
  private dataSourceDB: DataSourceBrowserDB;
  private inPlatformData?: Promise<Object>;
  constructor(
    connection: any,
    apexRestInput: ApexRestInput,
    apexRemoteInput: ApexRemoteInput,
    dataSourceDB: DataSourceBrowserDB,
    inPlatformData?: Promise<Object>
  ) {
    this.connection = connection;
    this.apexRestInput = apexRestInput;
    this.apexRemoteInput = apexRemoteInput;
    this.useRemote = false;
    this.apexRestInput.retryCount =
      this.apexRestInput.retryCount !== undefined
        ? this.apexRestInput.retryCount
        : Delay.defaultRetryCount;
    this.apexRemoteInput.retryCount =
      this.apexRemoteInput.retryCount !== undefined
        ? this.apexRemoteInput.retryCount
        : Delay.defaultRetryCount;
    this.apexRestInput.timeoutInMillis =
      this.apexRestInput.timeoutInMillis !== undefined
        ? this.apexRestInput.timeoutInMillis
        : Delay.defaultTimeout;
    this.apexRemoteInput.timeoutInMillis =
      this.apexRemoteInput.timeoutInMillis !== undefined
        ? this.apexRemoteInput.timeoutInMillis
        : Delay.defaultTimeout;
    this.dataSourceDB = dataSourceDB;
    this.inPlatformData = inPlatformData;
  }

  /**
   * Executes the DualDataSource
   * @returns Promise
   */
  public async execute(): Promise<any> {
    try {
      if (typeof this.inPlatformData === "function" && this.useRemote) {
        const dataHandler: any = this.inPlatformData;
        const apexRemoteData: any = {
          type: "apexremote",
          value: {
            className: this.apexRemoteInput.className,
            methodName: this.apexRemoteInput.methodName,
            inputMap: JSON.stringify(this.apexRemoteInput.inputMap),
            optionsMap: this.apexRemoteInput.optionsMap
          }
        };
        const response = await dataHandler(JSON.stringify(apexRemoteData));
        return JSON.parse(response);
      } else if (this.inPlatformData) {
        return this.inPlatformData;
      } else if (this.useRemote) {
        Logger.log("In Dual using ApexRemote", this.apexRemoteInput);
        const apexRemoteDataSource = new ApexRemoteDataSource(
          this.connection,
          this.apexRemoteInput,
          this.dataSourceDB
        );
        return await apexRemoteDataSource.execute();
      } else {
        Logger.log("In Dual using ApexRest", this.apexRestInput);
        const apexRestDataSource = new ApexRestDataSource(
          this.connection,
          this.apexRestInput,
          this.dataSourceDB
        );
        return await apexRestDataSource.execute();
      }
    } catch (error) {
      let errMsg =
        typeof error === "object" && error !== null
          ? JSON.stringify(error)
          : error;
      return Promise.reject(new Error(errMsg));
    }
  }
}
