import { BaseDataSourceInput } from "../common";

export class RestInput extends BaseDataSourceInput {
  public url;
  public options: RequestInit;
  constructor(restInput) {
    super(
      restInput.cacheable,
      restInput.cacheExpiration,
      restInput.retryCount,
      restInput.timeoutInMillis,
      restInput.reload,
      restInput.cacheType
    );
    this.url = restInput.url;
    this.options = restInput.options;
  }
  public digest(): Promise<string> {
    return super.digestData(this.url, JSON.stringify(this.options));
  }
}
