import * as jsforce from "jsforce/core";
import "jsforce/lib/api/apex";
import { BaseSDK } from "../common";
import DataSourceBrowserDB from "../common/DataSourceBrowserDB";
import { ApexRemoteDataSource, ApexRemoteInput } from "./apexremote";
import { ApexRestDataSource, ApexRestInput } from "./apexrest";
import { DataRaptor, DataRaptorInput } from "./dataraptor";
import IDataSource from "./DataSource.interface";
import DataSourceSDKConfig from "./DataSourceSDKConfig";
import { DualDataSource } from "./dual";
import {
  IntegrationProcedure,
  IntegrationProcedureInput
} from "./integrationprocedure";
import { RestDataSource } from "./rest";
import { RestInput } from "./rest/RestInput";
import { SoqlDataSource, SoqlInput } from "./soql";
import { SoslDataSource, SoslInput } from "./sosl";
import Delay from "./utils/delay";
/**
 * DataSource SDK
 * This SDK handles the connection with SalesForce and provide convenient ways to access Vlocity integration including
 * DataRaptor, Integration Procedure, Apex REST API etc.
 *
 * How to create a Datasource?
 *
 * @example Getting an instance of the DataSource
 * ```typescript
 * // Obtain a singleton dataSourceService
 * const dataSourceService = DataSource.getInstance({
 *   salesforceUrl: "https://www.salesforce.com",
 *   sessionId: "123",
 *   create: false,
 *   userId: "test" //additional data
 * });
 * ```
 *
 * @example Creating a custom datasource at runtime
 * ```typescript
 * const newDataSource = {
 *  oracle: () => {
 *   return "I'm a Custom implementation";
 *  },
 *  siebel: () => {
 *    return "I'm a Custom implementation";
 *  }
 * };
 *
 * dataSourceService.create(newDataSource, isNewInstance);
 *
 * // Create function implementation looks like this
 * // uses prototype to add methods to dataSourceService
 *
 * DataSource.mixin(DataSource, newDataSource, isNewInstance);
 * // call
 * dataSourceService
 *  .siebel()
 *  .execute(input)
 *  .then()
 *  .catch();
 * ```
 *
 * @example Using ApexRest
 * ```typescript
 * datasource
 *  .apexRest(new ApexRestInput({
 *   method: "get",
 *   url:
 *     "/vlocity_cmt/v2/cpq/carts/80137000000WlMP/products?hierarchy=1&pagesize=10&fields=IsActive,Id,Name,UnitPrice,ProductCode,jraju_card__RecurringPrice__c&includeIneligible=true"
 *  }))
 *   .execute()
 *   .then(resp => Logger.log("resp from thenable", resp))
 *   .catch(error => Logger.log("response from catch thenable", error));
 * ```
 *
 * @example Using Rest
 * ```typescript
 * const input1: RestInput = new RestInput({ url: "/test.json" });
 * datasource
 *   .rest(input1)
 *   .execute()
 *   .then(response => {
 *     Logger.log("call ", response);
 *   })
 *   .catch(error => {
 *     Logger.log("fail", error);
 *   });
 * ```
 * @example Using SOQL
 * ```typescript
 * datasource
 *   .soql(new SoqlInput({
 *    query: "SELECT Id, Name FROM Account LIMIT 10"
 *   })
 *   .execute()
 *   .then(response => {
 *     Logger.log("soql call ", response.records);
 *   })
 *   .catch(error => {
 *    Logger.log("soql fail", error);
 *   });
 * ```
 * @example SOSL call
 * ```typescript
 * datasource
 *   .sosl(new SoslInput({
 *    query: "FIND {Un*} IN ALL FIELDS RETURNING Account LIMIT 10"
 *   }))
 *   .execute()
 *   .then(response => {
 *     Logger.log("sosl call " + response.searchRecords);
 *   })
 *  .catch(error => {
 *     Logger.log("sosl fail" + error);
 *  });
 * ```
 *
 */
export default class DataSource extends BaseSDK {
  /**
   * Get an instance of DataSource if it exists. Otherwise, create a new one with the given config.
   * @param {config} Configuration to initialize DataSource.
   * @returns DataSource instance.
   */
  public static getInstance(config: DataSourceSDKConfig): DataSource {
    // If there is no instance and no config to determine how to create the instance, throw an error.
    if (!DataSource.instance && !config) {
      throw new Error(
        "DataSource:getInstance: DataSource cannot be instantiated properly. DataSourceSDKConfig must be provided."
      );
    }

    if (!DataSource.instance || (config && config.create)) {
      DataSource.instance = new DataSource(config);
    }
    return DataSource.instance;
  }

  /**
   * Private singleton instance of this SDK. There should always be only 1 instance.
   */
  private static instance: DataSource;
  /**
   * Config for this SDK.
   */
  public config: DataSourceSDKConfig;
  public connection: any;
  private dataSourceDB: DataSourceBrowserDB;
  /**
   * Create a new DataSource.
   * @param {config} Configuration for DataSource.
   */
  constructor(config) {
    super();
    this.config = config;
    this.dataSourceDB = new DataSourceBrowserDB(
      (this.config.dbName = "IndexedResponseDB"),
      (this.config.storeName = "DataSourceResponseStore")
    );
    this.dataSourceDB.createIndexedDBStore();
    if (this.config && this.config.jsforceConnection) {
      this.connection = this.config.jsforceConnection;
    } else if (this.config && this.config.sessionId && this.config.url) {
      const options: any = {
        serverUrl: this.config.url,
        sessionId: this.config.sessionId
      };
      if (this.config.proxyUrl) {
        options.proxyUrl = this.config.proxyUrl;
      }
      this.connection = new jsforce.Connection(options);
    }
  }

  // public updateConfig() {}

  /**
   * Create a new ApexRemote wrapper
   */
  public apexRemote(input: ApexRemoteInput) {
    return new ApexRemoteDataSource(
      this.connection,
      input,
      this.dataSourceDB,
      this.config.inPlatformData
    );
  }

  /**
   * Create a new ApexRest wrapper
   */
  public apexRest(input: ApexRestInput) {
    return new ApexRestDataSource(
      this.connection,
      input,
      this.dataSourceDB,
      this.config.inPlatformData
    );
  }

  /**
   * Create a new Rest wrapper
   */
  public rest(input: RestInput) {
    return new RestDataSource(
      input,
      this.dataSourceDB,
      this.config.inPlatformData
    );
  }

  /**
   * Create a new Soql wrapper
   */
  public soql(input: SoqlInput) {
    return new SoqlDataSource(
      this.connection,
      input,
      this.dataSourceDB,
      this.config.inPlatformData
    );
  }

  /**
   * Create a new Sosl wrapper
   * @param  {SoslInput} input
   */
  public sosl(input: SoslInput) {
    return new SoslDataSource(
      this.connection,
      input,
      this.dataSourceDB,
      this.config.inPlatformData
    );
  }

  /**
   * Create a new dataRaptor wrapper
   */
  public dataRaptor(input: DataRaptorInput) {
    return new DataRaptor(
      this.connection,
      input,
      this.dataSourceDB,
      this.config.inPlatformData
    );
  }

  /**
   * Create a new integrationProcedure wrapper
   */
  public integrationProcedure(input: IntegrationProcedureInput) {
    return new IntegrationProcedure(
      this.connection,
      input,
      this.dataSourceDB,
      this.config.inPlatformData
    );
  }

  /**
   * Create a dual data source that will automatically switch between Apex Rest or Apex Remote (if available).
   *
   * @param apexRestInput Apex Rest API Input.
   * @param apexRemoteInput Apex Remote Input.
   * @returns a instance of DualDataSource.
   */
  public dual(
    apexRestInput: ApexRestInput,
    apexRemoteInput: ApexRemoteInput
  ): IDataSource {
    const dual = new DualDataSource(
      this.connection,
      apexRestInput,
      apexRemoteInput,
      this.dataSourceDB,
      this.config.inPlatformData
    );
    dual.useRemote = this.config.useApexRemoteForDualDataSource;
    return dual;
  }
  /**
   * set a deafault retry count at datasource level
   * @param retryCount
   */
  public setRetryCount(retryCount: number) {
    Delay.setDefaultRetryCount(retryCount);
  }

  /**
   * set a deafault timeout at datasource level
   * @param timeout
   */
  public setRequestTimeout(timeout: number) {
    Delay.setDefaultTimeout(timeout);
  }
  /**
   * Get the cached response for the given cache key as generated from input object.
   * @param key is the generated from input object. For example, the key for ApexRestInput will be:
   * ```typescript
   * const key = apexRestInput.digest();
   * ```
   * @param cacheType is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public getCacheData(key, cacheType?) {
    return this.dataSourceDB.get(key, cacheType);
  }
  /**
   * sets the response for the given cache key as generated from input object.
   * @param key is the generated from input object. For example, the key for ApexRestInput will be:
   * ```typescript
   * const key = apexRestInput.digest();
   * ```
   * @param response is the data which needs to be cached.
   * @param cacheType is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public setCacheData(key, response, cacheType?) {
    return this.dataSourceDB.set(key, response, cacheType);
  }
  /**
   * deletes the cached response for the given cache key as generated from input object.
   * @param key is the generated from input object. For example, the key for ApexRestInput will be:
   * ```typescript
   * const key = apexRestInput.digest();
   * ```
   * @param cacheType is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public deleteCacheData(key, cacheType?) {
    return this.dataSourceDB.delete(key, cacheType);
  }
  /**
   * gets all keys for the given DB and store
   */
  public getCachedKeys() {
    return this.dataSourceDB.keys();
  }
  /**
   * clears the cache store
   */
  public clearCache() {
    return this.dataSourceDB.clear();
  }
  /**
   * gets cached Data if current Date time is within expiration time
   * @param key is generated from input object. For example, the key for ApexRestInput will be:
   * ```typescript
   * const key = apexRestInput.digest();
   * ```
   * @param type is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public getCachedResults(key, type?) {
    return this.dataSourceDB.getDataFromCache(key, false, type);
  }
  /**
   * sets results the cache based on the optional parameter value
   * @param key is generated from input object. For example, the key for ApexRestInput will be:
   * ```typescript
   * const key = apexRestInput.digest();
   * ```
   * @param value is the data which needs to be cached
   * @param cacheExpiraion
   * @param type is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public setCachedResults(key, value, cacheExpiraion, type?) {
    return this.dataSourceDB.setCache(key, value, cacheExpiraion, type);
  }
  /**
   * Create a DatasourceSDKConfig object.
   * @example
   * ```typescript
   *
   * // Instantiate DatasourceSDKConfig for login user
   * const datasourceSDKConfig = VlocitySDK.datasource.createConfigForDatasourceUser(salesforceUrl, sessionToken);
   *
   * // Instantiate the SDK itself
   * const datasource = VlocitySDK.datasource.getInstance(datasourceSDKConfig);
   * ```
   * @param {string} salesforceUrl
   * @param {string} sessionToken
   * @return {DataSourceSDKConfig} Configuration object to instantiate SDK.
   */
  public static createConfigForDatasourceUser(
    salesforceUrl: string,
    sessionToken: string,
    inPlatformData?: Promise<Object>
  ): DataSourceSDKConfig {
    const datasourceConfig = new DataSourceSDKConfig();
    datasourceConfig.sessionId = sessionToken;
    datasourceConfig.url = salesforceUrl;
    datasourceConfig.inPlatformData = inPlatformData;
    return datasourceConfig;
  }
  /**
   * creates new  apexRemoteInput
   *
   *@example
   * ```
   * // ApexRemoteInput
   * const apexRemoteInput = VlocitySDK.datasource.apexRemoteInput({
   * className:"className",
   * "methodName":"methodName"
   * });
   *```
   * @param parameters
   * {
   * cacheable:false
   * retryCount:0,
   *timeoutInMillis:20000,
   * reload:false,
   *className:"className",
   *methodName:"methodName",
   * paramsArray:paramsArray;
   *config:config;
   * }
   */
  public apexRemoteInput(parameters: Object): ApexRemoteInput {
    return new ApexRemoteInput(parameters);
  }
  /**
   * creates new  apexRestInput
   *
   *@example
   * ```
   * // ApexRestInput
   * const apexRestInput = VlocitySDK.datasource.apexRestInput({
   * path:"path",
   * method:"methodName"
   * });
   *```
   * @param parameters
   * {
   * cacheable:false
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * path:"path",
   * method:"method",
   * body:body;
   * }
   */
  public apexRestInput(parameters: Object): ApexRestInput {
    return new ApexRestInput(parameters);
  }
  /**
   * creates new  dataRaptorInput
   *
   *@example
   * ```
   * // DataraptorInput
   * const dataRaptorInput = VlocitySDK.datasource.dataRaptorInput({
   * bundleName:"name",
   * inputParamsString:"name=vlocity&state=ca"
   * });
   *```
   * @param parameters
   * {
   * inputParamsString:"name=vlocity&state=ca",
   * bundleName:"name"
   * cacheable:false
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public dataRaptorInput(parameters: Object): DataRaptorInput {
    return new DataRaptorInput(parameters);
  }
  /**
   * creates new  integrationProcedureInput
   *
   *@example
   * ```
   * // integrationProcedureInput
   * const integrationProcedureInput = VlocitySDK.datasource.integrationProcedureInput({
   * input: {},
   * optionsMap: {},
   * procedureKey: "TEST_SDK"
   * });
   *```
   * @param parameters
   * {
   *input: {},
   * optionsMap: {},
   * procedureKey: "TEST_SDK",
   * cacheable:false
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public integrationProcedureInput(
    parameters: Object
  ): IntegrationProcedureInput {
    return new IntegrationProcedureInput(parameters);
  }
  /**
   * creates new restInput
   *
   *@example
   * ```
   * // restInput
   * const restInput = VlocitySDK.datasource.restInput({
   * url: "/test"
   * });
   *```
   * @param parameters
   * {
   * url: "/test",
   *
   * cacheable:false,
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public restInput(parameters: Object): RestInput {
    return new RestInput(parameters);
  }
  /**
   * creates new soqlInput
   *
   *@example
   * ```
   * // soqlInput
   * const restInput = VlocitySDK.datasource.soqlInput({
   * query: "SELECT id from Account LIMIT 5"
   * });
   *```
   * @param parameters
   * {
   * query: "SELECT id from Account LIMIT 5",
   * cacheable:false,
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public soqlInput(parameters: Object): SoqlInput {
    return new SoqlInput(parameters);
  }
  /**
   * creates new soslInput
   *
   *@example
   * ```
   * // soslInput
   * const restInput = VlocitySDK.datasource.soslInput({
   * query: "FIND{ M1-5} IN ALL FIELDS RETURNING Account(Id, Name)"
   * });
   *```
   * @param parameters
   * {
   * query: "FIND{ M1-5} IN ALL FIELDS RETURNING Account(Id, Name)",
   * cacheable:false,
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public soslInput(parameters: Object): SoslInput {
    return new SoslInput(parameters);
  }
  /**
   * Create a DatasourceSDKConfig object.
   * @example
   * ```typescript
   *
   * // Instantiate DatasourceSDKConfig for login user
   * const datasourceSDKConfig = VlocitySDK.datasource.createConfigForLoginUser(salesforceUrl, sessionToken);
   *
   * // Instantiate the SDK itself
   * const datasource = VlocitySDK.datasource.getInstance(datasourceSDKConfig);
   * ```
   * @param {string} salesforceUrl
   * @param {string} sessionToken
   * @return {DataSourceSDKConfig} Configuration object to instantiate SDK.
   */
  public static createConfigForLoginUser(
    salesforceUrl: string,
    sessionToken: string,
    inPlatformData?: Promise<Object>
  ): DataSourceSDKConfig {
    return this.createConfigForDatasourceUser(
      salesforceUrl,
      sessionToken,
      inPlatformData
    );
  }

  /**
   * returns data from Apex remote call
   *
   *@example
   * ```
   *
   * VlocitySDK.datasource.handleApexRemoteCall({
   * className:"className",
   * "methodName":"methodName"
   * })
   *```
   * @param parameters
   * {
   * cacheable:false
   * retryCount:0,
   *timeoutInMillis:20000,
   * reload:false,
   *className:"className",
   *methodName:"methodName",
   * paramsArray:paramsArray;
   *config:config;
   * }
   */
  public handleApexRemoteCall(parameters: Object): Promise<any> {
    return this.apexRemote(this.apexRemoteInput(parameters)).execute();
  }
  /**
   *returns data from Apex Rest call
   *
   *@example
   * ```
   * VlocitySDK.datasource.handleApexRestCall({
   * path:"path",
   * method:"methodName"
   * });
   *```
   * @param parameters
   * {
   * cacheable:false
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * path:"path",
   * method:"method",
   * body:body;
   * }
   */
  public handleApexRestCall(parameters: Object): Promise<any> {
    return this.apexRest(this.apexRestInput(parameters)).execute();
  }
  /**
   * returns data from a dataRaptor call
   *@example
   * ```
   * VlocitySDK.datasource.handleDataRaptorCall({
   * bundleName:"name",
   * inputParamsString:"name=vlocity&state=ca"
   * });
   *```
   * @param parameters
   * {
   * inputParamsString:"name=vlocity&state=ca",
   * bundleName:"name"
   * cacheable:false
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public handleDataRaptorCall(parameters: Object): Promise<any> {
    return this.dataRaptor(this.dataRaptorInput(parameters)).execute();
  }
  /**
   *
   *@example
   * ```
   * returns data from a IntegrationProcedure
   * VlocitySDK.datasource.handleIntegrationProcedureCall({
   * input: {},
   * optionsMap: {},
   * procedureKey: "TEST_SDK"
   * });
   *```
   * @param parameters
   * {
   *input: {},
   * optionsMap: {},
   * procedureKey: "TEST_SDK",
   * cacheable:false
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public handleIntegrationProcedureCall(parameters: Object): Promise<any> {
    return this.integrationProcedure(
      this.integrationProcedureInput(parameters)
    ).execute();
  }
  /**
   * returns a rest Data
   *
   *@example
   * ```
   * VlocitySDK.datasource.handleRestCall({
   * url: "/test"
   * });
   *```
   * @param parameters
   * {
   * url: "/test",
   *
   * cacheable:false,
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public handleRestCall(parameters: Object): Promise<any> {
    return this.rest(this.restInput(parameters)).execute();
  }
  /**
   * returns SOQL data
   *
   *@example
   * ```
   * // soqlInput
   * VlocitySDK.datasource.handleSoqlCall({
   * query: "SELECT id from Account LIMIT 5"
   * });
   *```
   * @param parameters
   * {
   * query: "SELECT id from Account LIMIT 5",
   * cacheable:false,
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public handleSoqlCall(parameters: Object): Promise<any> {
    return this.soql(this.soqlInput(parameters)).execute();
  }
  /**
   * handles sosl call
   *
   *@example
   * ```
   * VlocitySDK.datasource.handleSoslCall({
   * query: "FIND{ M1-5} IN ALL FIELDS RETURNING Account(Id, Name)"
   * });
   *```
   * @param parameters
   * {
   * query: "FIND{ M1-5} IN ALL FIELDS RETURNING Account(Id, Name)",
   * cacheable:false,
   * retryCount:0,
   * timeoutInMillis:20000,
   * reload:false,
   * }
   */
  public handleSoslCall(parameters: Object): Promise<any> {
    return this.sosl(this.soslInput(parameters)).execute();
  }
}
