import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { B2BInput } from "../common/B2BInput";
/**
 * Class that defines input to get catalogs.
 * @extends B2BInput
 */
export class GetCatalogsInput extends B2BInput {
  public fieldsToInclude = [
    "ContextId",
    "pagesize",
    "offsetSize",
    "hierarchy",
    "fields",
    "sortBy"
  ];

  /**
   * The cart Id to be used as ContextId in the associated API to get list of catalogs.
   * It may be the regular cart id or the anonymous cart id.
   */
  public cartId?: string;

  /**
   * The hierarchy level to be retrieved.
   * For only the current level of catalogs, hierarchy will be 0.
   * For retrieving current level of catalogs with children, hierarchy will be 1.
   */
  public hierarchy?: number;

  /**
   * The offset to start the catalog list from.
   * If we are using pagesize of 20, the offset for next page will be 21.
   */
  public offsetSize?: number;

  /**
   * The number of top level catalogs to be returned.
   * Default is 20.
   */
  public pageSize?: number;

  /**
   * Sort the catalogs by the given sortBy
   * This will sort the returned catalogs first by Name in descending order, then description in ascending order.
   */
  public sortBy?: string[];

  /**
   * Parent catalog ID.
   * If this is provided, it will find only the child catalogs for this given catalog ID.
   */
  public catalogId?: string;

  /**
   * Remote class to handle get catalogs.
   */
  public remoteClass: string = "CpqAppHandler";

  /**
   * Remote method in the boave Remote Class to handle get catalogs.
   */
  public remoteMethod: string = "getCatalogHierarchy";

  /**
   * List of fields to be returned.
   * Supported Product2 and Pricebook fields.
   */
  public fields?: string[];

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    return (
      `${SalesForceNamespace.restAPIPrefix}/v2/cpq/catalogs` +
      this.getEncodedQueryString()
    );
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(cartItems?: any): any {
    return {};
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};
    this.fieldsToInclude.forEach(eachField => {
      if (this[eachField]) {
        inputMap[eachField] = this[eachField];
      }
    });

    if (this.cartId) {
      inputMap.ContextId = this.cartId;
    }

    if (this.fields) {
      inputMap.fields = this.fields.join(",");
    }

    if (this.sortBy) {
      inputMap.sortBy = this.sortBy.join(",");
    }

    return inputMap;
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      this.fieldsToInclude
    );

    return encodedQueryString;
  }
}
