import { SalesForceNamespace } from "../../common";
import { objectMap } from "../../utils";
/**
 * Class that defines each cartItem prices.
 */
export class Price {
  /**
   * This function parse the cartItem to fetch all pricing information.
   * @param cartItemResponse
   * @return {Price}
   */
  public static parse(cartItemResponse: any, input: any): Price {
    const priceDetails = new Price();
    const oneTimeTotal =
      cartItemResponse[SalesForceNamespace.namespace + "__OneTimeTotal__c"];
    const recurringTotal =
      cartItemResponse[SalesForceNamespace.namespace + "__RecurringTotal__c"];
    const oneTimeCharge =
      cartItemResponse[SalesForceNamespace.namespace + "__OneTimeCharge__c"];
    const recurringCharge =
      cartItemResponse[SalesForceNamespace.namespace + "__RecurringCharge__c"];
    const unitPrice = cartItemResponse.UnitPrice;
    const recurringPrice =
      cartItemResponse[SalesForceNamespace.namespace + "__RecurringPrice__c"];

    const priceKeys = [
      "actions",
      "originalValue",
      "previousValue",
      "value"
    ].concat(input.customFields);
    if (oneTimeTotal) {
      priceDetails.oneTimeTotal = objectMap([oneTimeTotal], priceKeys)[0];
    }
    if (recurringTotal) {
      priceDetails.recurringTotal = objectMap([recurringTotal], priceKeys)[0];
    }
    if (oneTimeCharge) {
      priceDetails.oneTimeCharge = objectMap([oneTimeCharge], priceKeys)[0];
    }
    if (recurringCharge) {
      priceDetails.recurringCharge = objectMap([recurringCharge], priceKeys)[0];
    }
    if (unitPrice) {
      priceDetails.unitPrice = unitPrice;
    }
    if (recurringPrice) {
      priceDetails.recurringPrice = recurringPrice;
    }
    if (input.customFields && input.customFields.length) {
      input.customFields.map(field => {
        if (cartItemResponse.hasOwnProperty(field)) {
          priceDetails.customFields[field] = cartItemResponse[field];
        }
      });
    }

    return priceDetails;
  }

  public oneTimeTotal?: any;
  public recurringTotal?: any;
  public oneTimeCharge?: any;
  public recurringCharge?: any;
  public unitPrice?: any;
  public recurringPrice?: any;

  /**
   * custom attributes for an offer
   */
  public customFields?: any = {};
}
