import "jsforce/lib/api/apex";
import { HttpMethod } from "../../common";
import DataSourceBrowserDB from "../../common/DataSourceBrowserDB";
import { Logger } from "../../logger";
import IDataSource from "../DataSource.interface";
import Delay from "../utils/delay";
import { isNode } from "../utils/isNode";
import { ApexRestInput } from "./";
const requestTimeoutError: string = "Request timed out";

export class ApexRestDataSource implements IDataSource {
  // @TODO: Avoid using any
  private connection: any;
  private input: ApexRestInput;
  private dataSourceDB: DataSourceBrowserDB;
  private inPlatformData: Promise<Object>;

  constructor(
    connection: any,
    input: ApexRestInput,
    dataSourceDB: DataSourceBrowserDB,
    inPlatformData?: Promise<Object>
  ) {
    this.connection = connection;
    this.input = input;
    this.input.retryCount =
      this.input.retryCount !== undefined
        ? this.input.retryCount
        : Delay.defaultRetryCount;
    this.input.timeoutInMillis =
      this.input.timeoutInMillis !== undefined
        ? this.input.timeoutInMillis
        : Delay.defaultTimeout;
    this.dataSourceDB = dataSourceDB;
    this.inPlatformData = inPlatformData;
  }
  /**
   * Executes the ApexRestDataSource
   * @returns Promise
   */
  public async execute(): Promise<any> {
    Logger.log("In ApexRest", this.input);
    try {
      if (this.inPlatformData) {
        return this.inPlatformData;
      } else if (!isNode && typeof this.input.digest === "function") {
        const iDBKey = await this.input.digest();
        return this.dataSourceDB
          .getDataFromCache(iDBKey, this.input.reload, this.input.cacheType)
          .catch(() => {
            return this.jsforceApexRest(this.input)
              .then(async result => {
                this.dataSourceDB.getCacheableValue(
                  this.input.cacheable,
                  this.connection.instanceUrl
                ) &&
                  (this.input.method === HttpMethod.GET ||
                    this.input.method === HttpMethod.POST) &&
                  (await this.dataSourceDB.setCache(
                    iDBKey,
                    result,
                    this.input.cacheExpiration,
                    this.input.cacheType
                  ));
                return result;
              })
              .catch(error => {
                return error;
              });
          });
      } else {
        return this.jsforceApexRest(this.input);
      }
    } catch (error) {
      let errMsg =
        typeof error === "object" && error !== null
          ? JSON.stringify(error)
          : error;
      return Promise.reject(new Error(errMsg));
    }
  }

  /**
   * get ApexRest
   * @param  {ApexRestInput} input
   * @returns Promise
   */
  private jsforceApexRest(input: ApexRestInput): Promise<any> {
    const defaultOptionsInput = {
      method: HttpMethod.GET,
      options: {},
      path: ""
    };
    const defaultOptions = new ApexRestInput(defaultOptionsInput);
    let customInput = new ApexRestInput({ ...defaultOptions, ...input });
    return new Promise((resolve, reject) => {
      let timeout = setTimeout(() => {
        reject(new Error(requestTimeoutError));
      }, this.input.timeoutInMillis);

      // Arguments are different for diff method types. Check jsforce documentation for more details.
      if (customInput.method.toLowerCase() === "get") {
        this.connection.apex.get(
          customInput.path,
          customInput.options,
          (error, response) => {
            clearTimeout(timeout);
            if (error) {
              if (this.input.retryCount === 0) {
                Logger.log("ApexRest failed", customInput, error);
                reject(error);
              } else {
                --this.input.retryCount;
                return Delay.addRetryDelay().then(() => {
                  return this.jsforceApexRest(input);
                });
              }
            }
            Logger.log("ApexRest is successful", customInput, response);
            resolve(response);
          }
        );
      } else {
        if (customInput.method.toLowerCase() === "delete") {
          this.connection.apex[customInput.method.toLowerCase()](
            customInput.path,
            customInput.options,
            (error, response) => {
              clearTimeout(timeout);
              if (error) {
                if (this.input.retryCount === 0) {
                  Logger.log("ApexRest failed", customInput, error);
                  reject(error);
                } else {
                  --this.input.retryCount;
                  return Delay.addRetryDelay().then(() => {
                    return this.jsforceApexRest(input)
                      .then(resolve)
                      .catch(reject);
                  });
                }
              }
              Logger.log("ApexRest is successful", customInput, response);
              resolve(response);
            }
          );
        } else {
          this.connection.apex[customInput.method.toLowerCase()](
            customInput.path,
            customInput.body,
            customInput.options,
            (error, response) => {
              clearTimeout(timeout);
              if (error) {
                if (this.input.retryCount === 0) {
                  Logger.log("ApexRest failed", customInput, error);
                  reject(error);
                } else {
                  --this.input.retryCount;
                  return Delay.addRetryDelay().then(() => {
                    return this.jsforceApexRest(input)
                      .then(resolve)
                      .catch(reject);
                  });
                }
              }
              Logger.log("ApexRest is successful", customInput, response);
              resolve(response);
            }
          );
        }
      }
    }).catch(error => {
      if (error.message === requestTimeoutError && this.input.retryCount > 0) {
        --this.input.retryCount;
        return Delay.addRetryDelay().then(() => {
          return this.jsforceApexRest(input);
        });
      }
    });
  }
}
