import DataSourceBrowserDB from "../../common/DataSourceBrowserDB";
import { SalesForceNamespace } from "../../common/SalesForceNamespace";
import { Logger } from "../../logger";
import IDataSource from "../DataSource.interface";
import Delay from "../utils/delay";
import { isNode } from "../utils/isNode";
import { ApexRemoteInput } from "./ApexRemoteInput";
declare const Visualforce: any;
export class ApexRemoteDataSource implements IDataSource {
  private connection: any;
  private input: ApexRemoteInput;
  private dataSourceDB: DataSourceBrowserDB;
  private inPlatformData: Promise<Object>;
  constructor(
    connection: any,
    input: ApexRemoteInput,
    dataSourceDB: DataSourceBrowserDB,
    inPlatformData?: Promise<Object>
  ) {
    this.connection = connection;
    this.input = input;
    this.input.retryCount =
      this.input.retryCount !== undefined
        ? this.input.retryCount
        : Delay.defaultRetryCount;
    this.input.timeoutInMillis =
      this.input.timeoutInMillis !== undefined
        ? this.input.timeoutInMillis
        : Delay.defaultTimeout;
    this.dataSourceDB = dataSourceDB;
    this.inPlatformData = inPlatformData;
  }

  /** Executes ApexRemote
   * @returns Promise
   */
  public async execute(): Promise<any> {
    try {
      Logger.log("ApexRemote", this.input);
      if (typeof this.inPlatformData === "function") {
        const dataHandler: any = this.inPlatformData;
        const apexRemoteData: any = {
          type: "apexremote",
          value: {
            className: this.input.className,
            methodName: this.input.methodName,
            inputMap: JSON.stringify(this.input.inputMap),
            optionsMap: this.input.optionsMap
          }
        };
        const response = await dataHandler(JSON.stringify(apexRemoteData));
        return JSON.parse(response);
      } else if (this.inPlatformData) {
        return this.inPlatformData;
      } else if (!isNode && typeof this.input.digest === "function") {
        const iDBKey = await this.input.digest();
        return this.dataSourceDB
          .getDataFromCache(iDBKey, this.input.reload, this.input.cacheType)
          .catch(() => {
            return this.visualForceRemoteInvoke(this.input)
              .then(async result => {
                this.dataSourceDB.getCacheableValue(
                  this.input.cacheable,
                  this.connection ? this.connection.instanceUrl : ""
                ) &&
                  (await this.dataSourceDB.setCache(
                    iDBKey,
                    result,
                    this.input.cacheExpiration,
                    this.input.cacheType
                  ));
                return result;
              })
              .catch(error => {
                return error;
              });
          });
      } else {
        return this.visualForceRemoteInvoke(this.input);
      }
    } catch (error) {
      let errMsg =
        typeof error === "object" && error !== null
          ? JSON.stringify(error)
          : error;
      return Promise.reject(new Error(errMsg));
    }
  }

  private visualForceRemoteInvoke(input: ApexRemoteInput): Promise<any> {
    // Format [namespace.][controller].[method]
    const remoteAction = `${SalesForceNamespace.namespace}.${input.className}.${
      input.methodName
    }`;

    // Don't escape apex method's response by default. User can override
    const defaultConfig = { escape: false };

    const config = { ...defaultConfig, ...input.config };
    return new Promise((resolve, reject) => {
      const remoteCallback = (result, event) => {
        Logger.log(result);
        // Parse result into object
        if (typeof result === "string") {
          result = JSON.parse(result);
        }

        if (event.statusCode < 400) {
          resolve(result);
        } else if (event.statusCode === 404 && this.input.retryCount !== 0) {
          --this.input.retryCount;
          return Delay.addRetryDelay().then(() => {
            return this.visualForceRemoteInvoke(input);
          });
        } else {
          reject(event);
        }
      };

      Visualforce.remoting.timeout = this.input.timeoutInMillis;

      // Visualforce global variable will be defined only in visualforce page
      Visualforce.remoting.Manager.invokeAction(
        remoteAction,
        ...(this.input.paramsArray || []),
        remoteCallback,
        config
      );
    }).catch(error => {
      return Promise.reject(new Error(error));
    });
  }
}
