import { SalesForceNamespace } from "../../common";
import { objectMap, sortItems } from "../../utils";
import { AttributeCategory } from "./AttributeCategory";
import { Price } from "./Price";
/**
 * Class that defines each cartItem.
 */
export class CartItem {
  /**
   * This function parse the cartItem.
   * @param cartItemResponse
   * @param rootBundleId
   * @param parentLineItem - optional
   * @return {CartItem}
   */
  public static parse(
    cartItemResponse: any,
    rootBundleId: string,
    input: any,
    parentLineItem?: any,
    hierarchy?: string
  ): CartItem {
    const cartItem = new CartItem();
    cartItemResponse = JSON.parse(JSON.stringify(cartItemResponse));
    cartItem.actions = cartItemResponse.actions;
    cartItem.sequenceNumber = cartItemResponse.sequenceNumber;
    cartItem.rootItemId = rootBundleId;
    cartItem.name =
      cartItemResponse.name ||
      (cartItemResponse.Name && cartItemResponse.Name.value);
    cartItem.description = cartItemResponse.Product2.Description;
    cartItem.productId = cartItemResponse.Product2.Id;
    cartItem.productHierarchyPath = cartItemResponse.productHierarchyPath;
    cartItem.defaultQuantity = cartItemResponse.defaultQuantity;
    cartItem.minQuantity = cartItemResponse.minQuantity;
    cartItem.maxQuantity = cartItemResponse.maxQuantity;
    cartItem.groupMinQuantity = cartItemResponse.groupMinQuantity;
    cartItem.groupMaxQuantity = cartItemResponse.groupMaxQuantity;
    cartItem.itemType = cartItemResponse.itemType;
    cartItem.promotions = cartItemResponse.promotions;
    cartItem.messages = cartItemResponse.messages;
    cartItem.failType = cartItemResponse.failType;
    cartItem.category = cartItemResponse.category;
    cartItem.versionLabel =
      cartItemResponse.Product2[
        SalesForceNamespace.namespace + "__VersionLabel__c"
      ];
    if (hierarchy) {
      cartItem.lineItemHierarchyPath =
        hierarchy + ">" + cartItemResponse.Id.value;
    } else {
      cartItem.lineItemHierarchyPath = cartItemResponse.Id.value;
    }
    if (
      cartItemResponse[SalesForceNamespace.namespace + "__AssetReferenceId__c"]
    ) {
      cartItem.assetReferenceId =
        cartItemResponse[
          SalesForceNamespace.namespace + "__AssetReferenceId__c"
        ].value;
    }

    if (cartItemResponse[SalesForceNamespace.namespace + "__Product2Id__r"]) {
      cartItem.objectTypeId =
        cartItemResponse[SalesForceNamespace.namespace + "__Product2Id__r"][
          SalesForceNamespace.namespace + "__ObjectTypeId__c"
        ];
    }
    if (cartItemResponse.SellingPeriod) {
      cartItem.sellingPeriod = cartItemResponse.SellingPeriod;
    }
    if (parentLineItem) {
      cartItem.parentId = parentLineItem.id;
      cartItem.parentAction = parentLineItem.action;
    }
    cartItem.action = cartItemResponse[
      SalesForceNamespace.namespace + "__Action__c"
    ]
      ? cartItemResponse[SalesForceNamespace.namespace + "__Action__c"].value
      : null;
    cartItem.id = cartItemResponse.Id
      ? cartItemResponse.Id.value
      : cartItemResponse.id.value;
    cartItem.PricebookEntry = cartItemResponse.PricebookEntry;
    cartItem.pricebookId =
      cartItemResponse.Pricebook2Id && cartItemResponse.Pricebook2Id.value
        ? cartItemResponse.Pricebook2Id.value
        : cartItemResponse.Pricebook2Id;
    cartItem.quantity = cartItemResponse.Quantity
      ? cartItemResponse.Quantity.value
      : cartItemResponse.quantity
        ? cartItemResponse.quantity.value
        : 0;
    cartItem.prices = Object.assign({}, Price.parse(cartItemResponse, input));
    cartItem.isVirtualItem = cartItemResponse.isVirtualItem;
    if (
      cartItemResponse[SalesForceNamespace.namespace + "__InCartQuantityMap__c"]
    ) {
      cartItem.inCartQuantityMap =
        cartItemResponse[
          SalesForceNamespace.namespace + "__InCartQuantityMap__c"
        ].value;
    }
    if (
      cartItemResponse.childProducts &&
      cartItemResponse.childProducts.records
    ) {
      cartItem.childProducts = [];
      cartItemResponse.childProducts.records = sortItems(
        cartItemResponse.childProducts.records,
        "sequenceNumber"
      );
      cartItemResponse.childProducts.records.forEach(childProduct => {
        if (childProduct.maxQuantity !== 0) {
          cartItem.childProducts.push(
            CartItem.parse(
              childProduct,
              rootBundleId,
              input,
              cartItem,
              cartItem.lineItemHierarchyPath
            )
          );
        }
      });
    }
    if (cartItemResponse.lineItems) {
      cartItem.lineItems = [];
      cartItemResponse.lineItems.records = sortItems(
        cartItemResponse.lineItems.records,
        "sequenceNumber"
      );
      cartItemResponse.lineItems.records.forEach(lineItem => {
        cartItem.lineItems.push(
          CartItem.parse(
            lineItem,
            rootBundleId,
            input,
            cartItem,
            cartItem.lineItemHierarchyPath
          )
        );
      });
    }
    if (cartItemResponse.productGroups) {
      cartItem.productGroups = [];
      cartItemResponse.productGroups.records.forEach(productGroup => {
        cartItem.productGroups.push(
          CartItem.parse(
            productGroup,
            rootBundleId,
            input,
            cartItem,
            cartItem.lineItemHierarchyPath
          )
        );
      });
    }

    /* Attributes */
    if (
      cartItemResponse.attributeCategories &&
      cartItemResponse.attributeCategories.records &&
      cartItemResponse.attributeCategories.records.length
    ) {
      cartItem.attributeCategories = [];
      cartItemResponse.attributeCategories.records.forEach(
        attributeCategory => {
          cartItem.attributeCategories.push(
            AttributeCategory.parse(attributeCategory, input, cartItem.id)
          );
        }
      );
    }

    if (input.customFields && input.customFields.length) {
      input.customFields.map(field => {
        if (cartItemResponse.hasOwnProperty(field)) {
          cartItem.customFields[field] = cartItemResponse[field];
        }
      });
    }

    if (input.columns && input.columns.length) {
      input.columns.map(column => {
        const obj = objectMap([cartItemResponse], [column.valueMap]);
        if (obj && obj.length) {
          const splittedKeys = column.valueMap.split(".");
          let objectKey = column.valueMap;
          if (splittedKeys.length > 1) {
            objectKey = splittedKeys[splittedKeys.length - 1];
          }
          cartItem.columns.push({
            value: obj[0][objectKey],
            label: column.label,
            valueMap: column.valueMap,
            dataType: column.dataType
          });
        }
      });
    }

    // get product relationship details
    if (input.productRelationshipRuleMap) {
      let productRelationshipRuleMap = JSON.parse(
        JSON.stringify(input.productRelationshipRuleMap)
      );
      let isProductIdExist = productRelationshipRuleMap[cartItem.productId];
      let isObjectTypeIdExist =
        productRelationshipRuleMap[cartItem.objectTypeId];
      let isRuleExist = isProductIdExist || isObjectTypeIdExist;
      if (isRuleExist) {
        let mappingId = isProductIdExist
          ? cartItem.productId
          : cartItem.objectTypeId;
        cartItem.productRelationshipRules =
          productRelationshipRuleMap[mappingId];
        cartItem.productRelationshipRules.forEach(rule => {
          rule["relatedItems"] = [];
        });

        if (
          input.lineItemRelationshipMap &&
          input.lineItemRelationshipMap.hasOwnProperty(cartItem.id)
        ) {
          let lineItemRelationshipMap = JSON.parse(
            JSON.stringify(input.lineItemRelationshipMap[cartItem.id])
          );
          cartItem.productRelationshipRules.forEach(rule => {
            let key1 = SalesForceNamespace.namespace + "__GlobalKey__c";
            if (lineItemRelationshipMap[rule[key1]]) {
              let nameKey =
                SalesForceNamespace.namespace + "__RelatedQuoteLineItemId__r";
              rule["relatedItems"] = lineItemRelationshipMap[rule[key1]];
              lineItemRelationshipMap[rule[key1]].forEach(relProd => {
                relProd["relatedItemName"] = relProd[nameKey]
                  ? relProd[nameKey]["Product2"]
                    ? relProd[nameKey]["Product2"]["Name"]
                    : ""
                  : "";
              });
            }
          });
        }
      }
    }

    return cartItem;
  }

  /**
   * Stores error messages
   */
  public messages: any[] = [];
  /**
   * API actions for CRUD operations
   */
  public actions: any[] = [];
  /**
   * Sequence number
   */
  public sequenceNumber: number;
  /**
   * Default Quantity
   */
  public defaultQuantity: number;
  /**
   * Quantity of an offer
   */
  public quantity: number;
  /**
   * Max Quantity of the offer
   */
  public maxQuantity: number;
  /**
   * Min Quantity of the offer
   */
  public minQuantity: number;
  /**
   * Group Min Quantity of the offer for cardinality check
   */
  public groupMinQuantity: number;
  /**
   * Group Max Quantity of the offer for cardinality check
   */
  public groupMaxQuantity: number;
  /**
   * Unique offer id
   */
  public id: string;
  /**
   *  context rule node failType : string
   */
  public failType: string;
  /**
   * context rule qualification result : string
   */
  public category: string;
  /**
   * Offer description
   */
  public description: string;

  /**
   * PricebookEntry node
   */
  public PricebookEntry: {};

  /**
   * Offer pricebook id
   */
  public pricebookId: string;
  /**
   * Offer productHierarchyPath
   */
  public productHierarchyPath: string;
  /**
   * boolean flag to reveal whether this offer is virtual item or not
   */
  public isVirtualItem: boolean;

  /**
   * boolean flag to reveal whether it is assets or not
   */
  public isAssets: boolean = false;

  /**
   * Offer name
   */
  public name: string;
  /**
   * Offer parentId which needs for CRUD operations.
   */
  public parentId: string;

  /**
   * Offer parentAction which needs for MACD operations.
   */
  public parentAction?: string;

  /**
   * Action which needs for MACD operations.
   */
  public action: string;

  /**
   * Root Bundle Product Id needs for CRUD operations.
   */
  public rootItemId: string;
  /**
   * Offer itemType eg: lineitem, productGroup
   */
  public itemType: string;
  /**
   * Offer product2.Id
   */
  public productId: string;
  /**
   * Offer SellingPeriod
   */
  public sellingPeriod: string;
  /**
   * Stores all prices information including action objects.
   */
  public prices: Price = {};
  /**
   * Offer inCartQuantityMap
   */
  public inCartQuantityMap?: any;
  /**
   * Child products of an offer.
   */
  public childProducts: CartItem[];
  /**
   * Line items of an offer.
   */
  public lineItems: CartItem[];
  /**
   * Product groups of an offer.
   */
  public productGroups: CartItem[];
  /**
   * Attributes of an offer.
   */
  public attributeCategories?: AttributeCategory[];

  /**
   * custom attributes for an offer
   */
  public customFields?: any = {};

  /**
   * custom columns for an offer
   */

  /**
   * Offer lineItemHierarchyPath - path of hierarchy of an item and its parent items
   */
  public lineItemHierarchyPath: string;

  public columns?: any[] = [];

  public promotions?: any = {};

  public productRelationshipRules = [];

  public objectTypeId?: string;

  public assetReferenceId?: string;

  public versionLabel?: string;
}
