  /**
   * Returns the working cart details.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for createWorkingCart to specify parameters
   * const input = b2bexpress.createWorkingCartInput();
   * input.inputMap = {SalesQuoteId = "1234"}; // use your sales quote Id
   * input.ipMethod = "create_WorkingCart"; // pass the VIP name
   *
   * // Invoke create_WorkingCart VIP via method createWorkingCart()
   * b2bexpress
   *   .createWorkingCart(input)
   *   .then(result => {
   *     Logger.info("Vlocity create working cart rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("Vlocity create working cart rest call failed" + error);
   *   });
   * ```
   * @param {CreateWorkingCartInput} input
   * @return {Promise<any>}
   * @throws "CreateWorkingCartInput::getRequestPayload() must have SalesQuoteId."
   * @throws "CreateWorkingCartInput::getRequestPayload() must have ipMethod."
   */
  public async createWorkingCart(input: CreateWorkingCartInput): Promise<any> {
    input = await this.createWorkingCartPreHook(input);
    return this.invokeIntegrationProcedure(input, HttpMethod.POST).then(
      async cartResponse => {
        // trigger post hook method
        cartResponse = await this.createWorkingCartPostHook(cartResponse);
        return cartResponse;
      }
    );
  }

