  /**
   * Add the product to the cart.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for addProductToCart to specify parameters
   * const input = b2bexpress.createAddProductToCartInput();
   * input.parentId = ""; // pass parent id if available. - optional.
   * input.rootBundleId = "1234"; // pass the root bundle Id.
   * input.actionObj = {}; // pass addtocart action object
   *
   * // Invoke /cpq/carts/:itemId/items API via method addProductToCart()
   * b2bexpress
   *   .addProductToCart(input)
   *   .then(result => {
   *     Logger.info("Vlocity add product to cart rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("Vlocity add product to cart rest call failed" + error);
   *   });
   * ```
   * @param {AddProductToCartInput} input
   * @return {Promise<any>}
   */
  public async addProductToCart(input: AddProductToCartInput): Promise<any> {
    const addToCartInput = input.getRequestPayload(this.cartItems);
    if (input.parentObject) {
      const configAddObject = { records: [{}] };
      configAddObject.records[0] = Object.assign({}, input.parentObject);

      const deleteArrayList = [
        ...this.deleteArrayListItems,
        "attributeCategories"
      ];
      deleteArrayList.forEach(element => {
        delete configAddObject.records[0][element];
      });

      if (input.actionObj.remote && input.actionObj.remote.params.items) {
        input.actionObj.remote.params.items[0].parentRecord = configAddObject;
        input.actionObj.remote.params = Object.assign(
          input.actionObj.remote.params,
          {
            configureAndAdd: false
          }
        );
      }
      if (input.actionObj.rest && input.actionObj.rest.params.items) {
        input.actionObj.rest.params.items[0].parentRecord = configAddObject;
        input.actionObj.rest.params = Object.assign(
          input.actionObj.rest.params,
          {
            configureAndAdd: false
          }
        );
      }
    }
    input = await this.addProductToCartPreHook(input);
    return this.invokeAction(input).then(async cartResponse => {
      let response: any = cartResponse;
      let parentItemObject: any = null; // parent of child product
      let parentRootBundle: any = null; // root bundle
      if (!cartResponse.actions) {
        this.cartItems.messages = cartResponse.messages || [];
        return Promise.reject(cartResponse);
      }
      if (input.parentId) {
        // adding child item to existing cart item
        // GetCartLineItemPrices
        const cartLineItemPricesInput = this.createGetCartLineItemPricesInput();
        cartLineItemPricesInput.actionObj =
          cartResponse.actions.itempricesupdated;
        const cartLineItemPrices = await this.getCartLineItemPrices(
          cartLineItemPricesInput
        );

        // Process cartItem response
        const currentCartItem = this.cartItems.records.find(
          cartItem => cartItem.Id.value === input.rootBundleId
        );
        const parentObject = CartItems.findAndReplaceLineItem(
          currentCartItem,
          input.parentObject.Id.value
        );

        response = await CartItems.update(
          parentObject,
          cartResponse,
          currentCartItem,
          cartLineItemPrices
        );
        parentObject.messages =
          cartResponse.records && cartResponse.records.length
            ? cartResponse.records[0].messages
            : [];
        const cartItemIndex = this.cartItems.records.findIndex(
          cartItem => cartItem.Id.value === input.rootBundleId
        );
        parentItemObject = parentObject;
        parentRootBundle = response;
        this.cartItems.records[cartItemIndex] = response;
      } else {
        // adding new cart item.
        if (!this.cartItems.records) {
          this.cartItems.records = [];
        }
        if (cartResponse.records) {
          this.cartItems.records.unshift(cartResponse.records[0]);
          parentItemObject = parentRootBundle = cartResponse.records[0];
        }
      }

      this.cartItems = CrossActions.processCrossActions(
        cartResponse.actions,
        this.cartItems,
        parentItemObject,
        parentRootBundle
      );

      // Get cart summary
      const cartSummaryInput = this.createGetCartSummaryInput();
      cartSummaryInput.cartId = input.cartId || this.cartItems.prices.orderId;
      const cartSummaryResponse = await this.getCartSummary(cartSummaryInput);

      let finalCartResponse = CartItems.parse(cartSummaryResponse, input);
      // trigger post hook method
      finalCartResponse = await this.addProductToCartPostHook(
        finalCartResponse,
        cartSummaryResponse
      );
      return finalCartResponse;
    });
  }

  /**
   * Update attributes to line item.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for itemsAttribute to specify parameters
   * const input = b2bexpress.createItemsAttributeInput();
   *
   * b2bexpress
   *   .itemsAttribute(input)
   *   .then(result => {
   *     Logger.info("Vlocity update product to cart rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("Vlocity update product to cart rest call failed" + error);
   *   });
   * ```
   * @param {itemsAttribute} input
   * @return {Promise<any>}
   */
  public async itemsAttribute(input: ItemsAttributeInput): Promise<any> {
    const updateCartInput = input.getRequestPayload(this.cartItems);
    const isRapidAttributeEnabled = input.isRapidAttributeEnabled;
    input = await this.itemsAttributePreHook(input);
    if (input.actionObj.remote && input.actionObj.remote.params) {
      input.actionObj.remote.params.items = input.items;
      input.actionObj.remote.params.cartId = input.cartId;
      input.actionObj.remote.params.filters = null;
      input.actionObj.remote.params.itemId = input.itemId;
      input.actionObj.remote.params.id = input.id;
    }
    return this.invokeAction(input).then(async cartResponse => {
      if (cartResponse.messages && cartResponse.messages[0].code === "161") {
        // Attributed Modified Successfully
        const modifiedItem = cartResponse.records[0];
        this.cartItems = CartItems.updateItemsAttribute(
          this.cartItems,
          modifiedItem,
          input
        );
        this.cartItems.messages = cartResponse.messages;
      }
      const finalCartResponse = await this.itemsAttributePostHook(cartResponse);
      if (isRapidAttributeEnabled) {
        return CartItems.parse(this.cartItems, input);
      } else {
        return finalCartResponse;
      }
    });
  }

  public itemsAttributePreHook(input: any): Promise<any> {
    return Promise.resolve(input);
  }

  public itemsAttributePostHook(
    cartResponse: any,
    originalCartResponse?: any
  ): Promise<any> {
    return Promise.resolve(cartResponse);
  }

