  /**
   * Update the line item to the cart.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for updateCartLineItem to specify parameters
   * const input = b2bexpress.createUpdateCartLineItemInput();
   * input.lineItemId = "1234"; // pass the updated line item product id.
   * input.rootBundleId = "1234"; // pass the root bundle Id.
   * input.actionObj = {}; // pass updateitems action object
   * input.lineItemDetails = { action: "updateQuantity", data: { value: 2 } };
   *
   * // Invoke /cpq/carts/:cartItemId/items PUT API via method updateCartLineItem()
   * b2bexpress
   *   .updateCartLineItem(input)
   *   .then(result => {
   *     Logger.info("Vlocity update product to cart rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("Vlocity update product to cart rest call failed" + error);
   *   });
   * ```
   * @param {UpdateCartLineItemInput} input
   * @return {Promise<any>}
   */
  public async updateCartLineItem(
    input: UpdateCartLineItemInput
  ): Promise<any> {
    const updateCartInput = input.getRequestPayload(this.cartItems);
    const rootBundleId = input.rootBundleId;
    let parentItemObject: any = null; // parent of child product
    let parentRootBundle: any = null; // root bundle
    let promiseResults = {};
    let cartSummaryResponse = {};
    const configUpdateObject: any = { records: [{}] };
    configUpdateObject.records[0] = Object.assign({}, input.itemObject);

    const deleteArrayList = [...this.deleteArrayListItems, "productGroups"];
    if (input.parentObject) {
      configUpdateObject.records[0] = Object.assign({}, input.parentObject);
      deleteArrayList.forEach(element => {
        delete configUpdateObject.records[0][element];
      });
      deleteArrayList.forEach(element => {
        delete input.itemObject[element];
      });
      configUpdateObject.records[0].lineItems = { records: [input.itemObject] };
    } else {
      configUpdateObject.records[0] = Object.assign({}, input.itemObject);
      deleteArrayList.forEach(element => {
        delete configUpdateObject.records[0][element];
      });
    }

    if (input.actionObj.remote && input.actionObj.remote.params) {
      input.actionObj.remote.params.items = configUpdateObject;
      if (input.commitToShoppingCart) {
        input.actionObj.remote.params = Object.assign(
          input.actionObj.remote.params,
          {
            // The enhanced API requires two extra params to indicate user wants to commit product selected to the anonymous cart:
            // bundleId & configureAndAdd
            bundleId: input.itemObject.Id.value,
            configureAndAdd: true
          }
        );
      }
    }
    if (input.actionObj.rest && input.actionObj.rest.params) {
      input.actionObj.rest.params.items = configUpdateObject;
      if (input.commitToShoppingCart) {
        input.actionObj.rest.params = Object.assign(
          input.actionObj.rest.params,
          {
            // The enhanced API requires two extra params to indicate user wants to commit product selected to the anonymous cart:
            // bundleId & configureAndAdd
            bundleId: input.itemObject.Id.value,
            configureAndAdd: true
          }
        );
      }
    }
    input = await this.updateCartLineItemPreHook(input);
    return this.invokeAction(input).then(async cartResponse => {
      if (!cartResponse.actions) {
        this.cartItems.messages = cartResponse.messages || [];
        return Promise.reject(cartResponse);
      }

      if (input.isRapidAttributeEnabled) {
        this.cartItems.messages = cartResponse.messages;
      }
      let cartLineItemPrices = null;
      if (
        !input.isRapidAttributeEnabled &&
        cartResponse.actions &&
        cartResponse.actions.itempricesupdated
      ) {
        // GetCartLineItemPrices
        const cartLineItemPricesInput = this.createGetCartLineItemPricesInput();
        cartLineItemPricesInput.actionObj =
          cartResponse.actions.itempricesupdated;
        // Get cart summary
        const cartSummaryInput = this.createGetCartSummaryInput();
        cartSummaryInput.cartId =
          (cartLineItemPricesInput.actionObj &&
            cartLineItemPricesInput.actionObj.remote.params.cartId) ||
          rootBundleId ||
          "";
        promiseResults = await Promise.all([
          this.getCartLineItemPrices(cartLineItemPricesInput),
          this.getCartSummary(cartSummaryInput)
        ]);
        cartLineItemPrices = (promiseResults && promiseResults[0]) || {};
        cartSummaryResponse = (promiseResults && promiseResults[1]) || {};
      }

      // Process cartItem response
      const currentCartItem = this.cartItems.records.find(
        cartItem => cartItem.Id.value === input.rootBundleId
      );
      let parentObject = null;
      if (input.parentId) {
        parentObject = CartItems.findAndReplaceLineItem(
          currentCartItem,
          input.parentId
        );
        parentItemObject = parentObject;
      }
      const response = await CartItems.update(
        parentObject || input.itemObject,
        cartResponse,
        currentCartItem,
        cartLineItemPrices
      );
      const cartItemIndex = this.cartItems.records.findIndex(
        cartItem => cartItem.Id.value === input.rootBundleId
      );
      this.cartItems.records[cartItemIndex] = response;
      if (!parentItemObject) {
        parentItemObject = parentRootBundle = cartResponse.records[0];
      } else {
        parentRootBundle = response;
      }
      this.cartItems = CrossActions.processCrossActions(
        cartResponse.actions,
        this.cartItems,
        parentObject,
        parentRootBundle
      );

      let finalCartResponse = input.isRapidAttributeEnabled
        ? CartItems.parse(this.cartItems, input)
        : CartItems.parse(cartSummaryResponse, input);
      // trigger post hook method
      finalCartResponse = await this.updateCartLineItemPostHook(
        finalCartResponse,
        cartSummaryResponse
      );
      return finalCartResponse;
    });
  }

