  /**
   * delete the line item to the cart.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for deleteCartItem to specify parameters
   * const input = b2bexpress.createDeleteCartItemInput();
   * input.lineItemId = "1234"; // pass the updated line item object id.
   * input.parentId = "1234"; // pass the parent object id if available - optional.
   * input.rootBundleId = "1234"; //  pass the root bundle Id.
   * input.actionObj = {}; // pass deleteitem action object
   *
   * // Invoke /cpq/carts/:cartItemId/items DELETE API via method deleteCartItem()
   * b2bexpress
   *   .deleteCartItem(input)
   *   .then(result => {
   *     Logger.info("Vlocity delete line item from cart rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("Vlocity delete line item from cart rest call failed" + error);
   *   });
   * ```
   * @param {DeleteCartItemInput} input
   * @return {Promise<any>}
   */
  public async deleteCartItem(input: DeleteCartItemInput): Promise<any> {
    const deleteCartItemInput = input.getRequestPayload(this.cartItems);
    const configDeleteObject = { records: [{}] };
    const deleteActionObj = input.itemObject.actions.deleteitem;
    let parentItemObject: any = null; // parent of child product
    let parentRootBundle: any = null; // root bundle
    if (input.parentObject) {
      configDeleteObject.records[0] = Object.assign({}, input.parentObject);
      const deleteArrayList = [
        ...this.deleteArrayListItems,
        "attributeCategories"
      ];
      deleteArrayList.forEach(element => {
        delete configDeleteObject.records[0][element];
      });
      if (deleteActionObj.remote && deleteActionObj.remote.params) {
        deleteActionObj.remote.params.items = [{ parentRecord: {} }];
        deleteActionObj.remote.params.items[0].parentRecord = configDeleteObject;
      }
      if (deleteActionObj.rest && deleteActionObj.rest.params) {
        deleteActionObj.rest.params.items = [{ parentRecord: {} }];
        deleteActionObj.rest.params.items[0].parentRecord = configDeleteObject;
      }
    }
    input = await this.deleteCartItemPreHook(input);
    return this.invokeAction(input).then(async cartResponse => {
      this.cartItems.crossActionMessages =
        cartResponse.messages && cartResponse.messages.length
          ? cartResponse.messages
          : null;
      if (input.rootBundleId === input.lineItemId) {
        const currentCartItemIndex = this.cartItems.records.findIndex(
          cartItem => cartItem.Id.value === input.rootBundleId
        );
        if (
          cartResponse &&
          cartResponse.records &&
          cartResponse.records[0].action === "Disconnect"
        ) {
          const cartItemIndex = this.cartItems.records.findIndex(
            cartItem => cartItem.Id.value === input.rootBundleId
          );
          this.cartItems.records[cartItemIndex] = cartResponse.records[0];
        } else {
          parentItemObject = parentRootBundle = this.cartItems.records[
            currentCartItemIndex
          ];
          this.cartItems.records.splice(currentCartItemIndex, 1);
          if (cartResponse.actions) {
            const itemAddedAction = cartResponse.actions.itemadded
              ? cartResponse.actions.itemadded.client
              : null;
            this.cartItems = CrossActions.processCrossActions(
              cartResponse.actions,
              this.cartItems,
              parentItemObject,
              parentRootBundle
            );
            // Checking if itemadded action has messages and if record node is empty.
            // If record is available then CrossActions.processCrossActions will take care of messages.
            if (
              itemAddedAction &&
              itemAddedAction.params.items &&
              !itemAddedAction.records
            ) {
              this.cartItems.crossActionMessages = this.cartItems.crossActionMessages.concat(
                itemAddedAction.params.items
              );
            }
          }
        }
      } else {
        // GetCartLineItemPrices
        const cartLineItemPricesInput = this.createGetCartLineItemPricesInput();
        if (
          cartResponse &&
          cartResponse.actions &&
          cartResponse.actions.itempricesupdated
        ) {
          cartLineItemPricesInput.actionObj =
            cartResponse.actions.itempricesupdated;
          const cartLineItemPrices = await this.getCartLineItemPrices(
            cartLineItemPricesInput
          );

          // Process cartItem response
          const currentCartItem = this.cartItems.records.find(
            cartItem => cartItem.Id.value === input.rootBundleId
          );
          // find parent object value
          const parentObject = CartItems.findAndReplaceLineItem(
            currentCartItem,
            input.parentObject.Id.value
          );
          const response = await CartItems.updateDeleteItem(
            parentObject,
            cartResponse,
            currentCartItem,
            cartLineItemPrices,
            input
          );

          const cartItemIndex = this.cartItems.records.findIndex(
            cartItem => cartItem.Id.value === input.rootBundleId
          );
          this.cartItems.records[cartItemIndex] = response;
          this.cartItems = CrossActions.processCrossActions(
            cartResponse.actions,
            this.cartItems,
            parentObject,
            response
          );
        }
      }

      // Get cart summary
      const cartSummaryInput = this.createGetCartSummaryInput();
      cartSummaryInput.cartId = this.cartItems.prices.orderId;
      const cartSummaryResponse = await this.getCartSummary(cartSummaryInput);

      let finalCartResponse = CartItems.parse(cartSummaryResponse, input);
      // trigger post hook method
      finalCartResponse = await this.deleteCartItemPostHook(
        finalCartResponse,
        cartResponse
      );

      finalCartResponse.cartMessages = finalCartResponse.messages;
      // passing message node to final response
      finalCartResponse.messages = cartResponse.messages;

      return finalCartResponse;
    });
  }

