  /**
   * get all cart items for the given cartId.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for getCartItems to specify parameters
   * const input = b2bexpress.createGetCartItemsInput();
   * input.cartId = "123"; // pass your cardId
   *
   * // Invoke /cpq/carts/:cartId/items GET API via method getCartItems()
   * b2bexpress
   *   .getCartItems(input)
   *   .then(result => {
   *     Logger.info("Vlocity get cart items rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("Vlocity get cart items rest call failed" + error);
   *   });
   * ```
   * @param {GetCartItemsInput} input
   * @return {Promise<any>}
   */
  public async getCartItems(input: GetCartItemsInput): Promise<any> {
    const cartSummaryInput = this.createGetCartSummaryInput();
    cartSummaryInput.cartId = input.cartId;
    input = await this.getCartItemsPreHook(input);
    const promiseResults = await Promise.all([
      this.invokeAPI(input, HttpMethod.GET),
      this.getCartSummary(cartSummaryInput)
    ]);
    let previousRecords = [];
    if (input.actionObj.remote.params.lastRecordId)
      previousRecords = [...this.cartItems.records];
    this.cartItems.records =
      (promiseResults[0] && promiseResults[0].records) || [];
    let finalCartResponse = CartItems.parse(this.cartItems, input);
    // trigger post hook method
    finalCartResponse = await this.getCartItemsPostHook(
      finalCartResponse,
      promiseResults[1] || {}
    );
    this.cartItems.records = [...previousRecords, ...this.cartItems.records];
    return finalCartResponse;
  }

