  /**
   * apply adjustments for the given cartItem.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for ApplyAdjustmentInput to specify parameters
   * const input = b2bexpress.createApplyAdjustmentInput();
   * input.actionObj = cartItem.vlocity_cmt__RecurringCharge__c.actions.applyadjustment;
   * input.adjustmentData = {'detailType':'Override','method':'ABSOLUTE', 'value':50};
   *
   * // Invoke /cpq/carts/:cartItemId/items/:itemId/pricing  POST API via method applyAdjustment()
   * b2bexpress
   *   .applyAdjustment(input)
   *   .then(result => {
   *     Logger.info("Vlocity apply adjustment rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("Vlocity apply adjustment rest call failed" + error);
   *   });
   * ```
   * @param {GetCartSummaryInput} input
   * @return {Promise<any>}
   */
  public async applyAdjustment(input: ApplyAdjustmentInput): Promise<any> {
    if (
      input.actionObj.remote &&
      input.actionObj.remote.params &&
      input.actionObj.remote.params.adjustments
    ) {
      input.actionObj.remote.params.adjustments[0] = Object.assign(
        input.actionObj.remote.params.adjustments[0],
        {
          AdjustmentMethod: input.adjustmentData.method,
          AdjustmentValue: parseFloat(input.adjustmentData.value),
          DetailType: input.adjustmentData.detailType
        }
      );
      if (input.adjustmentData.timePlan) {
        input.actionObj.remote.params.adjustments[0].TimePlan =
          input.adjustmentData.timePlan;
        input.actionObj.remote.params.adjustments[0].TimePolicy =
          input.adjustmentData.timePolicy;
      }
    }
    if (
      input.actionObj.rest &&
      input.actionObj.rest.params &&
      input.actionObj.rest.params.adjustments
    ) {
      input.actionObj.rest.params.adjustments[0] = Object.assign(
        input.actionObj.rest.params.adjustments[0],
        {
          AdjustmentMethod: input.adjustmentData.method,
          AdjustmentValue: parseFloat(input.adjustmentData.value),
          DetailType: input.adjustmentData.detailType
        }
      );
      if (input.adjustmentData.timePlan) {
        input.actionObj.rest.params.adjustments[0].TimePlan =
          input.adjustmentData.timePlan;
        input.actionObj.rest.params.adjustments[0].TimePolicy =
          input.adjustmentData.timePolicy;
      }
    }
    input = await this.applyAdjustmentsPreHook(input);
    const response = await this.invokeAction(input);
    const adjustmentMessages =
      response.messages && response.messages.length ? response.messages : null;
    if (!response.records) {
      this.cartItems.crossActionMessages = adjustmentMessages;
      return CartItems.parse(this.cartItems, input);
    }
    this.cartItems.records = response.records;
    // Get cart summary
    const cartSummaryInput = this.createGetCartSummaryInput();
    cartSummaryInput.cartId = this.cartItems.prices.orderId;
    const cartSummaryResponse = await this.getCartSummary(cartSummaryInput);
    cartSummaryResponse.crossActionMessages = adjustmentMessages;

    let applyAdjustmentResponse = CartItems.parse(cartSummaryResponse, input);
    // trigger post hook method
    applyAdjustmentResponse = await this.applyAdjustmentsPostHook(
      applyAdjustmentResponse,
      response
    );
    return applyAdjustmentResponse;
  }

