  /**
   * Clone Line Items in the cart.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for createCloneItemsInput to specify parameters
   * const input = b2bexpress.createCloneItemsInput();
   * input.actionObj = cartItem.actions.cloneitem;
   *
   * // Invoke /cpq/carts/:cartItemId/items/clone  POST API via method cloneItems()
   * b2bexpress
   *   .cloneItems(input)
   *   .then(result => {
   *     Logger.info("Vlocity clone items in the cart rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("Vlocity clone items in the cart rest call failed" + error);
   *   });
   * ```
   * @param {CloneItemsInput} input
   * @return {Promise<any>}
   */
  public async cloneItems(input: CloneItemsInput): Promise<any> {
    input = await this.cloneItemsPreHook(input);
    const cartResponse = await this.invokeAction(input);
    let response: any = cartResponse;
    let parentItemObject: any = null; // parent of child product
    let parentRootBundle: any = null; // root bundle
    if (!response.records) {
      this.cartItems.messages = response.messages || [];
      return Promise.reject(response);
    }

    if (input.parentId) {
      // adding child item to existing cart item
      // GetCartLineItemPrices
      const cartLineItemPricesInput = this.createGetCartLineItemPricesInput();
      cartLineItemPricesInput.actionObj = response.actions.itempricesupdated;
      const cartLineItemPrices = await this.getCartLineItemPrices(
        cartLineItemPricesInput
      );

      // Process cartItem response
      const currentCartItem = this.cartItems.records.find(
        cartItem => cartItem.Id.value === input.rootBundleId
      );
      const parentObject = CartItems.findAndReplaceLineItem(
        currentCartItem,
        input.parentId
      );

      response = await CartItems.clone(
        parentObject,
        cartResponse,
        currentCartItem,
        cartLineItemPrices
      );
      const cartItemIndex = this.cartItems.records.findIndex(
        cartItem => cartItem.Id.value === input.rootBundleId
      );
      parentItemObject = parentObject;
      parentRootBundle = response;
      this.cartItems.records[cartItemIndex] = response;
    } else {
      // adding new cart item.
      if (!this.cartItems.records) {
        this.cartItems.records = [];
      }
      if (cartResponse.records) {
        this.cartItems.records.unshift(cartResponse.records[0]);
        parentItemObject = parentRootBundle = cartResponse.records[0];
      }
    }

    this.cartItems = CrossActions.processCrossActions(
      cartResponse.actions,
      this.cartItems,
      parentItemObject,
      parentRootBundle
    );

    // Get cart summary
    const cartSummaryInput = this.createGetCartSummaryInput();
    cartSummaryInput.cartId = this.cartItems.prices.orderId;
    const cartSummaryResponse = await this.getCartSummary(cartSummaryInput);

    let cloneItemsResponse = CartItems.parse(cartSummaryResponse, input);
    // trigger post hook method
    cloneItemsResponse = await this.cloneItemsPostHook(
      cloneItemsResponse,
      response
    );
    return cloneItemsResponse;
  }

