  /**
   * Delete Price Adjustments for the Line Items in the cart.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for createDeletePriceAdjustmentInput to specify parameters
   * const input = b2bexpress.createDeletePriceAdjustmentInput();
   * input.actionObj = priceDetails.actions.deleteAdjustment;
   *
   * // Invoke "/cpq/carts/:cartId/items/:lineItemId/pricing/:adjustmentId" DELETE API via method deletePriceAdjustments()
   * b2bexpress
   *   .deletePriceAdjustments(input)
   *   .then(result => {
   *     Logger.info("Vlocity delete adjustments rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("Vlocity delete adjustments rest call failed" + error);
   *   });
   * ```
   * @param {DeletePriceAdjustmentInput} input
   * @return {Promise<any>}
   */
  public async deletePriceAdjustments(
    input: DeletePriceAdjustmentInput
  ): Promise<any> {
    input = await this.deletePriceAdjustmentsPreHook(input);
    const response = await this.invokeAction(input);
    if (!response.records) {
      return this.cartItems;
    }
    this.cartItems.records = response.records;
    // Get cart summary
    const cartSummaryInput = this.createGetCartSummaryInput();
    cartSummaryInput.cartId = this.cartItems.prices.orderId;
    const cartSummaryResponse = await this.getCartSummary(cartSummaryInput);

    let adjustmentResponse = CartItems.parse(cartSummaryResponse, input);
    // trigger post hook method
    adjustmentResponse = await this.deletePriceAdjustmentsPostHook(
      adjustmentResponse,
      this.cartItems
    );
    return adjustmentResponse;
  }

