  /**
   * Returns a VIP call response.
   * * @example
   * ```typescript
   * const input = {
   *   ipMethod: "create_WorkingCart"; // pass the VIP name,
   *   inputMap: {SalesQuoteId = "1234"}
   * }
   *
   * // Invoke your API with invokeAction method
   * b2bExpressSDK
   * .invokeIntegrationProcedure(input)
   * .then(response => {
   *   Logger.info(
   *     "Invoke VIP Response" + response
   *   );
   * })
   * .catch(error => {
   *   Logger.info(
   *     "Invoke VIP call failed" + error
   *   );
   * });
   * ```
   * @param {B2BInput} input
   * @param {HttpMethod} httpMethod
   * @return {Promise<any>}
   * @throws Error - needs to be caught
   */
  private async invokeIntegrationProcedure(
    input: B2BInput,
    httpMethod: HttpMethod
  ): Promise<any> {
    const apexRestInput: ApexRestInput = new ApexRestInput({
      body: input.getRequestPayload
        ? input.getRequestPayload()
        : input.inputMap,
      method: httpMethod
    });
    if (input.chainable) {
      apexRestInput.options = { headers: { chainable: true } };
    }

    const integrationProcedureOptions = {
      optionsMap: input.optionsMap,
      procedureKey: input.ipMethod,
      input: input.getRequestPayload
        ? input.getRequestPayload()
        : input.inputMap,
      cacheable: input.cacheable,
      rest: apexRestInput,
      reload: input.reload,
      cacheExpiration:
        input.cacheExpiration || new Date(new Date().getTime() + 5 * 60000), // default 15 min
      timeoutInMillis: input.timeoutInMillis
    };
    const integrationProcedureInput = this.datasource.integrationProcedureInput(
      integrationProcedureOptions
    );
    return await this.datasource
      .integrationProcedure(integrationProcedureInput)
      .execute();
  }

  /**
   * Invoke an action returning from previous API call.
   * @example
   * ```typescript
   *
   * // Instantiate the input object to specify parameters
   * const input = b2bexpress.createUpdateCartLineItemInput();
   * input.actionObj = {
   *     client: {
   *       records: [],
   *       params: {}
   *     },
   *     remote: {
   *       params: {}
   *     },
   *     rest: {
   *       params: {},
   *       method: "POST",
   *       link: "/v2/carts/:cartId/items"
   *     };
   *
   * // Invoke your API with invokeAction method
   * b2bExpressSDK
   * .invokeAction(input)
   * .then(response => {
   *   Logger.info(
   *     "Invoke Action Response" + response
   *   );
   * })
   * .catch(error => {
   *   Logger.info(
   *     "Invoke Action call failed" + error
   *   );
   * });
   * ```
   * @param {object} input
   * @return {Promise<any>}
   */
  private async invokeAction(input: any): Promise<any> {
    if (this.context.hasLogin) {
      const apexRestInput: ApexRestInput = new ApexRestInput({
        body: input.actionObj.rest.params,
        method: input.actionObj.rest.method,
        path: input.actionObj.rest.link,
        cacheable: input.cacheable || false,
        reload: input.reload || false,
        cacheExpiration:
          input.cacheExpiration || new Date(new Date().getTime() + 5 * 60000), // default 15 min
        timeoutInMillis: input.timeoutInMillis || 20000
      });
      const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
        className: input.remoteClass,
        inputMap: input.actionObj.remote.params || {},
        methodName:
          (input.actionObj.remote &&
            input.actionObj.remote.params.methodName) ||
          input.remoteMethod,
        cacheable: input.cacheable || false,
        reload: input.reload || false,
        cacheExpiration:
          input.cacheExpiration || new Date(new Date().getTime() + 5 * 60000),
        timeoutInMillis: input.timeoutInMillis || 20000
      });
      return await this.datasource
        .dual(apexRestInput, apexRemoteInput)
        .execute();
    }
  }

  /**
   * Returns a API call response.
   * @param {B2BInput} input
   * @param {HttpMethod} httpMethod
   * @return {Promise<any>}
   * @throws Error - needs to be caught
   */
  private async invokeAPI(
    input: B2BInput,
    httpMethod: HttpMethod
  ): Promise<any> {
    if (this.context.hasLogin) {
      const apexRestInput: ApexRestInput = new ApexRestInput({
        body: input.getRequestPayload(),
        method: httpMethod,
        path: input.getAPIPath(),
        cacheable: input.cacheable,
        reload: input.reload,
        cacheExpiration:
          input.cacheExpiration || new Date(new Date().getTime() + 5 * 60000), // default 15 min
        timeoutInMillis: input.timeoutInMillis
      });
      const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
        className: input.remoteClass,
        inputMap: (input.actionObj && input.actionObj.remote.params) || {},
        methodName:
          (input.actionObj && input.actionObj.remote.params.methodName) ||
          input.remoteMethod,
        cacheable: input.cacheable,
        reload: input.reload,
        cacheExpiration:
          input.cacheExpiration || new Date(new Date().getTime() + 5 * 60000),
        timeoutInMillis: input.timeoutInMillis
      });
      return await this.datasource
        .dual(apexRestInput, apexRemoteInput)
        .execute();
    } else {
      // for Anonymous users
      const restUrl = input.getAnonymousSiteRestUrl();
      let restOptions: RestInput;
      if (httpMethod === "POST" || httpMethod === "PUT") {
        restOptions = new RestInput({
          options: {
            body:
              typeof input.getRequestPayload() === "object"
                ? JSON.stringify(input.getRequestPayload())
                : input.getRequestPayload(),
            headers: {
              Authorization: B2BSDKConfig.authToken,
              "Content-Type": input.contentType
            },
            method: httpMethod
          },
          url: restUrl,
          cacheable: input.cacheable,
          reload: input.reload,
          cacheExpiration:
            input.cacheExpiration || new Date(new Date().getTime() + 5 * 60000),
          timeoutInMillis: input.timeoutInMillis
        });
      } else {
        restOptions = new RestInput({
          options: {
            headers: {
              Authorization: B2BSDKConfig.authToken
            },
            method: httpMethod
          },
          url: restUrl,
          cacheable: input.cacheable,
          reload: input.reload,
          cacheExpiration:
            input.cacheExpiration || new Date(new Date().getTime() + 5 * 60000),
          timeoutInMillis: input.timeoutInMillis
        });
      }
      return await this.datasource.rest(restOptions).execute();
    }
  }
