import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { B2BInput } from "../common/B2BInput";
/**
 * Class that defines input to Get cart Summary.
 * @extends B2BInput
 */
export class GetCartSummaryInput extends B2BInput {
  public fieldsToInclude = ["validate", "price", "translation", "fields"];
  /**
   * The cart Id to be used for getting cart Summary.
   * It may be the regular cart id or the anonymous cart id.
   */
  public cartId?: string = "";

  /**
   * List of fields to be returned.
   * Supported Product2 and Pricebook fields.
   */
  public fields?: string;

  /**
   * Flag to indicate if validation rules should be run.
   */
  public validate: boolean = false;

  /**
   * Flag to indicate if pricing rules should be run.
   */
  public price: boolean = false;

  /**
   * Remote class to handle get cart Summary.
   */
  public remoteClass: string = "CpqAppHandler";

  /**
   * Remote method in the above Remote Class to handle get cart Summary.
   */
  public remoteMethod: string = "getCarts";

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    if (!this.cartId) {
      throw new Error("GetCartSummaryInput::getAPIPath() must have cartId.");
    }
    return (
      `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${this.cartId}` +
      this.getEncodedQueryString()
    );
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(cartItems?: any): any {
    const requestBody: any = {};
    return requestBody;
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};
    this.fieldsToInclude.forEach(eachField => {
      if (this[eachField]) {
        inputMap[eachField] = this[eachField];
      }
    });

    if (!this.validate) {
      inputMap.validate = this.validate;
    }

    if (!this.price) {
      inputMap.price = this.price;
    }

    return inputMap;
  }

  public prepareGetCartSummaryActionObject() {
    if (!this.cartId) {
      throw new Error("GetCartSummaryInput::getAPIPath() must have cartId.");
    }
    const actionObj = {
      client: {
        params: {}
      },
      remote: {
        methodName: this.remoteMethod,
        params: {
          cartId: this.cartId,
          methodName: this.remoteMethod,
          price: this.price,
          validate: this.validate
        }
      },
      rest: {
        method: "GET",
        link: this.getAPIPath(),
        params: {
          cartId: this.cartId,
          methodName: this.remoteMethod,
          price: this.price,
          validate: this.validate
        }
      }
    };
    if (!this.actionObj) {
      this.actionObj = actionObj;
    }
    return this.actionObj;
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      this.fieldsToInclude
    );

    return encodedQueryString;
  }
}
