/**
 * Function that constructs the query string
 * @param inputObject
 * @param withQuestionMarkPrefix
 * @param fieldsToInclude - Optional Collection of field names that must be included in the query string. All fields are used if this is undefined or an empty array
 * @returns {string}
 */
export function queryStringEncode(
  inputObject: any,
  withQuestionMarkPrefix: boolean = true,
  fieldsToInclude?: string[]
): string {
  let queryString: string;
  queryString = Object.entries(inputObject)
    .filter(([key]) => {
      return (
        !fieldsToInclude ||
        fieldsToInclude.length === 0 ||
        fieldsToInclude.indexOf(key) >= 0
      );
    })
    .map(([key, value]) => `${key}=${encodeURIComponent(String(value))}`)
    .join("&");
  if (!queryString) {
    return "";
  }
  if (withQuestionMarkPrefix) {
    queryString = `?${queryString}`;
  }

  return queryString;
}
