/**
 * Function that constructs the object mapping for given keys
 * @param mappingObject - array of objects
 * @param keysToInclude - array of strings. For multilevel keys use (.) operator. eg: ['name','id.value']
 * @param excludeKeysInResponse - Optional value from user to exclude keys in response.
 * @returns {array}
 */

export function objectMap(
  mappingObject: any,
  keysToInclude: string[],
  excludeKeysInResponse?: boolean
): any {
  const result = [];
  try {
    mappingObject.forEach(obj => {
      const mappedObj = {};
      keysToInclude.forEach(key => {
        if (key) {
          const keyObj = retriveCustomKey(key);
          key = keyObj.key;
          const splittedKeys = key.split(".");
          if (splittedKeys.length > 1) {
            mappedObj[
              keyObj.customKey || splittedKeys[splittedKeys.length - 1]
            ] = splittedKeys.reduce((curObj, curKey) => {
              return curObj[curKey];
            }, obj);
          } else {
            mappedObj[keyObj.customKey || key] = obj[key];
          }
        }
      });
      if (excludeKeysInResponse) {
        result.push(Object.values(mappedObj).toString());
      } else {
        result.push(mappedObj);
      }
    });
  } catch (e) {
    return [];
  }
  return JSON.parse(JSON.stringify(result));
}

function retriveCustomKey(key: string): any {
  const splittedKeys = key.split("#");
  const keyNameObject = { customKey: null, key: splittedKeys[0] };
  if (splittedKeys.length > 1) {
    keyNameObject.customKey = splittedKeys[1];
  }
  return keyNameObject;
}
