export default class ResponseError extends Error {
  public requestOptions: string | object;
  public response: Response;
  public status: string;

  /**
   * Construct a new ResponseError
   * @param {string} message - an message to return instead of the the default error message
   * @param {string | object} requestOptions - the request
   * @param {Object} response - the object returned by datasource
   */
  constructor(message, requestOptions, response) {
    super(message);
    this.requestOptions = requestOptions;
    this.response = (response || {}).response || response;
    this.status = response.status;
  }
}
