import { isFunction, isObject } from "util";
import { Logger } from "../logger";
import { SalesForceNamespace } from "./SalesForceNamespace";

/**
 * Base SDK abstract class.
 * Base class from which other SDK classes are derived
 */

// SDK_VERSION global variable is defined in webpack.config.js
declare const SDK_VERSION: string;

export abstract class BaseSDK {
  /**
   * Adds all the enumerable string keyed function properties of a source
   * object to the sdk prototype.
   * `.extend` should only be used to add new methods and it won't override the existing methods.
   *
   * **Note:** If the property already exists, it will be not be added.
   *
   * Custom functions which are being extended should have proper namespaces to avoid issues during upgrades.
   * When Vlocity releases new changes it won't impact if unique namespaces are used.
   *
   * Example: `myCompanyOrFeatureMethodName`
   *
   * @example **How to extend an sdk?**
   *
   * ```
   * VlocitySDK.datasource.extend({ myCompanySayHello(){
   *      console.log("Hello World");
   *   }, sayTime() {
   *      console.log(new Date().myCompanyGetTime());
   *   }});
   *
   * const dataSource = VlocitySDK.datasource.getInstance({'create':true});
   * console.log(daSource.myCompanySayHello())
   * ```
   *
   * @param extendObj The object of functions
   */
  public static extend(extendObj: object) {
    if (!isObject(extendObj)) {
      throw new Error(
        "Extend method expects an object with functions as properties"
      );
    }
    const properties = Object.keys(extendObj);

    const methodNames = properties.filter(methodName =>
      // isFunction supports async and generator functions as well
      isFunction(extendObj[methodName])
    );

    methodNames.forEach(methodName => {
      if (!this.prototype.hasOwnProperty(methodName)) {
        this.prototype[methodName] = extendObj[methodName];
      } else {
        Logger.log(
          `SDK extend method failed to extend ${methodName} as it already exists`
        );
      }
    });
  }

  /**
   * Adds all the enumerable string keyed function properties of a source
   * object to the sdk prototype.
   *
   * `.override` method should only be used to override the existing methods and should only be used in rare cases.
   * Overriding the existing or default methods may cause unintended consequences and may also impact during upgrades.
   *
   * **Please be cautious while using this**
   *
   * @example **How to override methods in an sdk?**
   *
   * ```
   * VlocitySDK.datasource.override({ soql(){
   *      console.log("This code overrides the default soql function");
   *      // code goes here...
   *   }, sosl() {
   *      console.log("This code overrides the default soql function");
   *      // code goes here...
   *   }});
   *
   * const dataSource = VlocitySDK.datasource.getInstance({'create':true});
   * console.log(dataSource.soql()); // prints "This code overrides the default soql function"
   * ```
   * @param overrideObj The object of functions
   */
  public static override(overrideObj: object) {
    if (!isObject(overrideObj)) {
      throw new Error("Override method expects an object with properties");
    }
    // @TODO: Not restricting to just functions/methods. All properties can be overridden.
    // Needs to be revisited to finalize the restrictions
    Object.assign(this.prototype, overrideObj);
  }

  // SDK_VERSION will be replaced with package.json version during build
  private sdkVersion: string = SDK_VERSION;

  /**
   * Returns the version number of SDK.
   * @return {string} Returns SDK version number as string
   */
  public version(): string {
    return this.sdkVersion;
  }

  /** getter method for namespace. Provides the salesforce org namespace */
  get namespace(): string {
    return SalesForceNamespace.namespace;
  }

  /** setter method for namespace. Set custom namespace
   * @param {string} Salesforce Namespace.
   */
  set namespace(customNamespace: string) {
    SalesForceNamespace.namespace = customNamespace;
  }
}
