export enum LogLevel {
  debug,
  log,
  info,
  warn,
  error,
  none
}

/**
 * Logger supports 5 log levels and also can suppress logs altogether.
 *
 * The order is a follows:
 * debug < log < info < warn < error
 *
 * Log types above the current log level will be captured.
 * If you set the log level to `info`, `warn` and `errors` will still be logged, but `debug` and
 * `log` logs will not be logged.
 * Use log level 'none' to suppress all logs
 */
export class Logger {
  public static debug(...args: any[]): void {
    Logger.emitLogMessage("debug", args);
  }

  public static info(...args: any[]): void {
    Logger.emitLogMessage("info", args);
  }

  public static log(...args: any[]): void {
    Logger.emitLogMessage("log", args);
  }

  public static warn(...args: any[]): void {
    Logger.emitLogMessage("warn", args);
  }

  public static error(...args: any[]): void {
    Logger.emitLogMessage("error", args);
  }

  public static getLogLevel(): LogLevel {
    return this.debugLevel;
  }

  public static setLogLevel(value: LogLevel) {
    if (!(value in LogLevel)) {
      throw new Error("Invalid value `value` assigned to `logLevel`");
    }
    this.debugLevel = value;
  }
  // Default log level is info
  private static debugLevel: LogLevel = LogLevel.info;

  private static emitLogMessage(
    type: "debug" | "info" | "log" | "warn" | "error",
    args: any[]
  ) {
    if (LogLevel[type] < Logger.debugLevel) {
      return;
    }
    console[type](...args);
  }
}
