import DataSourceBrowserDB from "../../common/DataSourceBrowserDB";
import { SalesForceNamespace } from "../../common/SalesForceNamespace";
import { Logger } from "../../logger";
import * as UrlUtil from "../../utils/url.util";
import { ApexRemoteDataSource, ApexRemoteInput } from "../apexremote";
import { ApexRestDataSource, ApexRestInput } from "../apexrest";
import IDataSource from "../DataSource.interface";
import Delay from "../utils/delay";
import { IntegrationProcedureInput } from "./";
declare const Visualforce: any;
export class IntegrationProcedure implements IDataSource {
  // @TODO: Avoid using any
  private connection: any;
  private input: IntegrationProcedureInput;
  private apexRestInput: ApexRestInput;
  private apexRemoteInput: ApexRemoteInput;
  private dataSourceDB: DataSourceBrowserDB;
  private actionMode: boolean;
  private inPlatformData?: Promise<Object>;

  constructor(
    connection: any,
    input: IntegrationProcedureInput,
    dataSourceDB: DataSourceBrowserDB,
    inPlatformData?: Promise<Object>
  ) {
    this.connection = connection;
    this.input = input;
    this.input.retryCount =
      this.input.retryCount !== undefined
        ? this.input.retryCount
        : Delay.defaultRetryCount;
    this.input.timeoutInMillis =
      this.input.timeoutInMillis !== undefined
        ? this.input.timeoutInMillis
        : Delay.defaultTimeout;
    this.dataSourceDB = dataSourceDB;
    this.inPlatformData = inPlatformData;
  }

  /**
   * Executes the Integration Procedure
   *
   * @returns promise
   */
  public async execute(): Promise<any> {
    Logger.log("Integration Procedure call input", this.input);

    this.actionMode = typeof Visualforce !== "undefined" ? true : false;
    if (typeof this.inPlatformData === "function") {
      let dataHandler: any = this.inPlatformData;
      let ipData: any = {
        type: "integrationprocedure",
        value: {
          ipMethod: this.input.procedureKey,
          inputMap: JSON.stringify(this.input.input),
          optionsMap: this.input.optionsMap
        }
      };
      const response = await dataHandler(JSON.stringify(ipData));
      return JSON.parse(response);
    } else if (this.inPlatformData) {
      return this.inPlatformData;
    }
    if (this.actionMode) {
      const ns = SalesForceNamespace.namespace;
      const clsName = this.input.remote.className;
      const mtdName = this.input.remote.methodName;

      if (
        !window[ns] ||
        !window[ns][clsName] ||
        !window[ns][clsName][mtdName]
      ) {
        throw new Error("Remote Action not found.");
      }

      const integrationProcedureInput: ApexRemoteInput = new ApexRemoteInput({
        className: clsName,
        methodName: mtdName,
        paramsArray: [
          this.input.procedureKey,
          this.input.input,
          this.input.optionsMap
        ],
        retryCount: this.input.retryCount,
        timeoutInMillis: this.input.timeoutInMillis,
        cacheable: this.input.cacheable,
        cacheExpiration: this.input.cacheExpiration,
        reload: this.input.reload,
        cacheType: this.input.cacheType
      });
      const apexRemoteDataSource = new ApexRemoteDataSource(
        this.connection,
        integrationProcedureInput,
        this.dataSourceDB
      );
      const ipResultRemote = await apexRemoteDataSource
        .execute()
        .then(response => {
          return this.ipResponse(response);
        })
        .catch(error => {
          return error;
        });

      return ipResultRemote;
    }

    const integrationProcedurePath: ApexRestInput = new ApexRestInput({
      body: this.input.rest ? this.input.rest.body : {},
      method: this.input.rest ? this.input.rest.method : "GET",
      options: this.input.rest ? this.input.rest.options : {},
      path: this.getIntegrationProcedurePath(
        this.input.procedureKey,
        this.input.input
      ),
      retryCount: this.input.retryCount,
      timeoutInMillis: this.input.timeoutInMillis,
      cacheable: this.input.cacheable,
      cacheExpiration: this.input.cacheExpiration,
      reload: this.input.reload,
      cacheType: this.input.cacheType
    });

    const apexRestDataSource = new ApexRestDataSource(
      this.connection,
      integrationProcedurePath,
      this.dataSourceDB
    );
    const ipResultRest = await apexRestDataSource
      .execute()
      .then(response => {
        return this.ipResponse(response);
      })
      .catch(error => {
        return error;
      });

    return ipResultRest;
  }

  private getIntegrationProcedurePath(procedureKey: string, inputMap: object) {
    let queryString = "";
    if (inputMap) {
      queryString = UrlUtil.queryStringEncode(inputMap);
    }
    const integrationProcedurePath = `${
      SalesForceNamespace.restAPIPrefix
    }/v1/integrationprocedure/${procedureKey}${queryString}`;
    return integrationProcedurePath;
  }

  private ipResponse(response: string) {
    let obj;

    try {
      obj = typeof response === "string" ? JSON.parse(response) : response;
    } catch (e) {
      throw new Error(e);
    }

    if (
      obj &&
      obj.hasOwnProperty("vlcIPData") &&
      obj.hasOwnProperty("vlcStatus") &&
      obj.vlcStatus === "InProgress"
    ) {
      if (this.actionMode) {
        this.input.optionsMap = obj;
      } else {
        this.input.rest.options.headers = obj;
      }

      return this.execute();
    }

    return obj;
  }
}
