import { SalesForceNamespace } from "../../common";
import { CrossActionsService } from "../crossActions/CrossActionsService";
import { CartItem } from "./CartItem";
/**
 * Class that defines all cartItems.
 */
export class CartItems {
  /**
   * This function parse the cartItem.
   * @param cartItemResponse
   * @return {CartItems}
   */
  public static parse(cartItemsResponse: any, input: any): CartItems {
    const cartItems = new CartItems();
    cartItems.messages = cartItemsResponse.messages;
    cartItems.prices = cartItemsResponse.prices;
    cartItems.records = [];

    if (
      cartItemsResponse.actions &&
      cartItemsResponse.actions.nextproducts &&
      cartItemsResponse.actions.nextproducts.remote &&
      cartItemsResponse.actions.nextproducts.remote.params &&
      cartItemsResponse.actions.nextproducts.remote.params.offsetSize
    ) {
      cartItems.offsetSize =
        cartItemsResponse.actions.nextproducts.remote.params.offsetSize;
    }

    if (cartItemsResponse.records) {
      cartItemsResponse.records.forEach(cartItem => {
        cartItems.records.push(
          CartItem.parse(cartItem, cartItem.Id.value, input)
        );
      });
      cartItems.records.forEach(cartItem => {
        if (
          cartItem.action === "Disconnect" ||
          cartItem.action === "Change" ||
          cartItem.action === "Existing"
        ) {
          cartItem.isAssets = true;
        }
      });
    }
    if (cartItemsResponse.crossActionMessages) {
      cartItems.crossActionMessages = cartItemsResponse.crossActionMessages;
    }
    if (input.customFields && input.customFields.length) {
      input.customFields.map(field => {
        if (cartItemsResponse.hasOwnProperty(field)) {
          cartItems.customFields[field] = cartItemsResponse[field];
        }
      });
    }
    if (cartItems.messages && cartItems.messages.length) {
      cartItems.messages.forEach(message => {
        if (message.messageId) {
          if (!message.bundleId) {
            message.bundleId = message.messageId.split("|")[0];
          }
          const bundleIndex = cartItems.records.findIndex(
            cartItem => cartItem.id === message.bundleId
          );
          if (bundleIndex !== -1 && message.severity === "ERROR") {
            const lineItemId = message.messageId.split("|")[0];
            // const hierarchyPath = messageId.split("<");
            // messageId = hierarchyPath[hierarchyPath.length - 1];
            const lineItem = CrossActionsService.searchLineItem(
              cartItems.records[bundleIndex],
              lineItemId
            );
            if (
              lineItem &&
              -1 ===
                lineItem.messages.findIndex(
                  msg => msg.messageId === message.messageId
                )
            ) {
              lineItem.messages.push(message);
            }
          }
        }
      });
    }
    return cartItems;
  }

  /**
   * This function updates updated product to the cartItem/bundle.
   * @param parentObj
   * @param lineItemObj
   * @param parentRootBundle
   * @param updatedPrices
   * @return {Promise<any>}
   */
  public static async update(
    parentObj: any,
    lineItemObj: any,
    parentRootBundle: any,
    updatedPrices: any
  ): Promise<any> {
    const cartItemsObj = new CartItems();
    const inCartQuantityMap =
      SalesForceNamespace.namespace + "__InCartQuantityMap__c";
    // if parent root bundle cart item
    if (lineItemObj.records && !lineItemObj.records[0].lineItems) {
      lineItemObj.records[0].lineItems = parentObj.lineItems;
      lineItemObj.records[0].childProducts = parentObj.childProducts;
      lineItemObj.records[0].productGroups = parentObj.productGroups;
      lineItemObj.records[0].actions = parentObj.actions;
      await cartItemsObj.replacePrices(parentObj, lineItemObj.records, false);
      parentObj[inCartQuantityMap] = lineItemObj.records[0][inCartQuantityMap];
    } else if (lineItemObj.records) {
      // if line item updated
      parentObj[inCartQuantityMap] = lineItemObj.records[0][inCartQuantityMap];
      const response = cartItemsObj.insertLineItemToParent(
        parentObj,
        lineItemObj.records[0].lineItems.records[0]
      );
    }
    // replace updated line item into cart items
    if (parentObj.Id.value === parentRootBundle.Id.value && parentObj.Name) {
      parentRootBundle = parentObj;
    } else if (parentObj.Id.value === parentRootBundle.Id.value) {
      parentRootBundle.lineItems = parentObj.lineItems;
      parentRootBundle.productGroups = parentObj.productGroups;
      parentRootBundle.childProducts = parentObj.childProducts;
      parentRootBundle[inCartQuantityMap] = parentObj[inCartQuantityMap];
    } else {
      const itemToBeReplaced = CartItems.findAndReplaceLineItem(
        parentRootBundle,
        parentObj.Id.value,
        parentObj
      );
    }
    if (!parentRootBundle.Name) {
      parentObj.lineItems = parentRootBundle.lineItems;
    }
    if (!updatedPrices) {
      return parentRootBundle;
    }
    // update prices
    const updatedCartItemWithPrices = await cartItemsObj.updatePrices(
      parentRootBundle,
      updatedPrices.records
    );
    if (lineItemObj.messages) {
      updatedCartItemWithPrices.messages = lineItemObj.messages;
    }
    return updatedCartItemWithPrices;
  }

  /**
   * This function updates cloned product to the cartItem/bundle.
   * @param parentObj
   * @param lineItemObj
   * @param parentRootBundle
   * @param updatedPrices
   * @return {Promise<any>}
   */
  public static async clone(
    parentObj: any,
    lineItemObj: any,
    parentRootBundle: any,
    updatedPrices: any
  ): Promise<any> {
    const cartItemsObj = new CartItems();
    const inCartQuantityMap =
      SalesForceNamespace.namespace + "__InCartQuantityMap__c";
    // if parent root bundle cart item
    const objectToBeUpdated = CartItems.findAndReplaceLineItem(
      lineItemObj.records[0],
      parentObj.Id.value
    );

    if (objectToBeUpdated && objectToBeUpdated.lineItems) {
      objectToBeUpdated.lineItems.records.forEach(lineItemRecord => {
        cartItemsObj.insertLineItemToParent(parentObj, lineItemRecord);
      });
    }
    if (objectToBeUpdated && objectToBeUpdated.productGroups) {
      parentObj.productGroups = objectToBeUpdated.productGroups;
    }
    parentObj[inCartQuantityMap] = objectToBeUpdated[inCartQuantityMap];
    if (!updatedPrices) {
      return parentRootBundle;
    }
    // update prices
    const clonedCartItemWithPrices = await cartItemsObj.updatePrices(
      parentRootBundle,
      updatedPrices.records
    );
    if (lineItemObj.messages) {
      clonedCartItemWithPrices.messages = lineItemObj.messages;
    }
    return clonedCartItemWithPrices;
  }

  /**
   * This function remove lineItem and add it back to child products array.
   * Also update InCartQuantityMap Object.
   * @param parentObj
   * @param childProductObj
   * @param parentRootBundle
   * @param updatedPrices
   * @return {Promise<any>}
   */
  public static async updateDeleteItem(
    parentObj: any,
    childProductObj: any,
    parentRootBundle: any,
    updatedPrices: any,
    deleteItemInput?: any
  ): Promise<any> {
    const cartItemsObj = new CartItems();
    const inCartQuantityMapField =
      SalesForceNamespace.namespace + "__InCartQuantityMap__c";
    // replace lineItem when disconenct lineItem in MACD flow
    const cartItemIndex = childProductObj.records.findIndex(
      cartItem => cartItem.Id.value === deleteItemInput.rootBundleId
    );
    if (
      childProductObj &&
      childProductObj.records[cartItemIndex].lineItems &&
      childProductObj.records[cartItemIndex].action !== "Add"
    ) {
      const deletedLineItem = CartItems.findAndReplaceLineItem(
        childProductObj.records[cartItemIndex],
        deleteItemInput.itemObject.Id.value
      );
      const itemToBeReplaced = CartItems.findAndReplaceLineItem(
        parentObj,
        deletedLineItem.Id.value,
        deletedLineItem
      );

      const updatedParent = CartItems.findAndReplaceLineItem(
        childProductObj.records[cartItemIndex],
        parentObj.Id.value
      );

      parentObj[inCartQuantityMapField].value =
        updatedParent[inCartQuantityMapField].value;

      if (childProductObj.records[cartItemIndex].childProducts) {
        if (
          parentObj.childProducts &&
          parentObj.childProducts.records &&
          parentObj.childProducts.records.length &&
          childProductObj.records[cartItemIndex].childProducts.records &&
          childProductObj.records[cartItemIndex].childProducts.records.length
        ) {
          parentObj.childProducts.records.push(
            childProductObj.records[cartItemIndex].childProducts.records[0]
          );
        } else {
          parentObj.childProducts =
            childProductObj.records[cartItemIndex].childProducts;
        }
      }
    } else {
      if (deleteItemInput && deleteItemInput.itemObject) {
        if (childProductObj && childProductObj.records) {
          const cartItem = childProductObj.records[cartItemIndex];
          // Check if cartItem has childProducts and if the child product exists
          if (
            cartItem.childProducts &&
            cartItem.childProducts.records &&
            cartItem.childProducts.records[0]
          ) {
            deleteItemInput.itemObject.UnitPrice =
              cartItem.childProducts.records[0].UnitPrice;
          }
        }

        cartItemsObj.removeLineItemFromParent(
          parentObj,
          deleteItemInput.itemObject
        );
      }
    }
    if (parentObj.Id.value === parentRootBundle.Id.value && parentObj.Name) {
      parentRootBundle = parentObj;
    } else if (parentObj.Id.value === parentRootBundle.Id.value) {
      parentRootBundle.lineItems = parentObj.lineItems;
      parentRootBundle.productGroups = parentObj.productGroups;
      parentRootBundle.childProducts = parentObj.childProducts;
      parentRootBundle[inCartQuantityMapField] =
        parentObj[inCartQuantityMapField];
    } else {
      const itemToBeReplaced = CartItems.findAndReplaceLineItem(
        parentRootBundle,
        parentObj.Id.value,
        parentObj
      );
    }
    const updatedCartItemWithPrices = await cartItemsObj.updatePrices(
      parentRootBundle,
      updatedPrices.records
    );
    return updatedCartItemWithPrices;
  }

  /**
   * This function will search an productGroup/LineItem with in given bundle
   * @param product
   * @param idToBeSearched
   * @return {any}
   */
  public static findAndReplaceLineItem(
    product,
    idToBeSearched,
    replaceItem?: any,
    returnParent?: boolean
  ) {
    // base check the required object
    if (product.Id.value === idToBeSearched) {
      if (replaceItem) {
        product = replaceItem;
      }
      return product;
    }
    if (product.productGroups) {
      for (let i = 0; i < product.productGroups.records.length; i++) {
        let foundProductGroup = this.findAndReplaceLineItem(
          product.productGroups.records[i],
          idToBeSearched,
          replaceItem,
          returnParent
        );
        if (foundProductGroup) {
          if (replaceItem) {
            foundProductGroup = replaceItem;
            if (
              product.productGroups.records[i].Id.value === replaceItem.Id.value
            ) {
              product.productGroups.records[i] = replaceItem;
            }
          }
          if (returnParent) {
            return {
              lineItem: product.productGroups.records[i]
            };
          }
          // If the product group was found in the recursive call, bubble it up.
          return foundProductGroup;
        }
      }
    }
    if (product.lineItems) {
      for (let i = 0; i < product.lineItems.records.length; i++) {
        let foundLineItem = this.findAndReplaceLineItem(
          product.lineItems.records[i],
          idToBeSearched,
          replaceItem,
          returnParent
        );
        if (foundLineItem) {
          if (replaceItem) {
            foundLineItem = replaceItem;
            if (
              product.lineItems.records[i].Id.value === replaceItem.Id.value
            ) {
              product.lineItems.records[i] = replaceItem;
            }
          }
          if (returnParent) {
            return {
              lineItem: product.lineItems.records[i]
            };
          }
          // If the line item was found in the recursive call, bubble it up.
          return foundLineItem;
        }
      }
    }
  }

  /**
   * This function will search an productGroup/LineItem with in given bundle
   * @param product
   * @param idToBeSearched
   * @return {any}
   */
  public static findAndReplaceLineItemInCart(
    product,
    idToBeSearched,
    replaceItem?: any,
    returnParent?: boolean
  ) {
    // base check the required object
    if (product.Id.value === idToBeSearched) {
      if (replaceItem) {
        product = replaceItem;
      }
      return product;
    }
    if (product.productGroups) {
      for (let i = 0; i < product.productGroups.records.length; i++) {
        let foundProductGroup = this.findAndReplaceLineItemInCart(
          product.productGroups.records[i],
          idToBeSearched,
          replaceItem,
          returnParent
        );
        if (foundProductGroup) {
          if (replaceItem) {
            foundProductGroup = replaceItem;
            if (
              product.productGroups.records[i].Id.value === replaceItem.Id.value
            ) {
              product.productGroups.records[i] = replaceItem;
            }
          }
          if (returnParent) {
            if (foundProductGroup.returnParent === false) {
              return {
                lineItem: foundProductGroup.lineItem,
                returnParent: false
              };
            } else {
              return {
                lineItem: product,
                returnParent: false
              };
            }
          }
          // If the product group was found in the recursive call, bubble it up.
          return foundProductGroup;
        }
      }
    }
    if (product.lineItems) {
      for (let i = 0; i < product.lineItems.records.length; i++) {
        let foundLineItem = this.findAndReplaceLineItemInCart(
          product.lineItems.records[i],
          idToBeSearched,
          replaceItem,
          returnParent
        );
        if (foundLineItem) {
          if (replaceItem) {
            foundLineItem = replaceItem;
            if (
              product.lineItems.records[i].Id.value === replaceItem.Id.value
            ) {
              product.lineItems.records[i] = replaceItem;
            }
          }
          if (returnParent) {
            if (foundLineItem.returnParent === false) {
              return {
                lineItem: foundLineItem.lineItem,
                returnParent: false
              };
            } else {
              return {
                lineItem: product,
                returnParent: false
              };
            }
          }
          // If the line item was found in the recursive call, bubble it up.
          return foundLineItem;
        }
      }
    }
  }

  public static updateItemsAttribute(
    cartItems: CartItems,
    itemResponse: any,
    input: any
  ) {
    let itemToBeUpdated;
    const index = 0;
    for (let index = 0; index < cartItems.records.length; index++) {
      const element = cartItems.records[index];
      const itemFound = CartItems.findAndReplaceLineItem(
        element,
        itemResponse.Id.value
      );
      if (itemFound) {
        itemToBeUpdated = itemFound;
      }
    }
    /* Attributes */
    if (itemToBeUpdated && itemResponse) {
      itemToBeUpdated.attributeCategories = itemResponse.attributeCategories;
      itemToBeUpdated.PricebookEntry = itemResponse.PricebookEntry;
      return cartItems;
    }
  }

  /**
   * This function will search for given line item id and return its root item and index of root item in the cart
   * @param listOfItemsInCart
   * @param idToBeSearched
   * @return {any}
   */
  public static findRootFromGivenLineItem(listOfItemsInCart, idToBeSearched) {
    if (listOfItemsInCart && listOfItemsInCart.length) {
      for (
        let itemIndex = 0;
        itemIndex < listOfItemsInCart.length;
        itemIndex++
      ) {
        const rootItemDetails = this.findAndReplaceLineItem(
          listOfItemsInCart[itemIndex],
          idToBeSearched
        );
        if (rootItemDetails) {
          return {
            index: itemIndex,
            lineItem: listOfItemsInCart[itemIndex]
          };
        }
      }
    }
    return null;
  }

  /**
   * stores offsetsize.
   */
  public offsetSize: number;

  /**
   * Stores error messages.
   */
  public messages: any[] = [];

  /**
   * Stores error messages.
   */
  public cartMessages: any[] = [];

  /**
   * Stores cross action messages.
   */
  public crossActionMessages: any[] = [];

  /**
   * Stores all cartItems.
   */
  public records: CartItem[] = [];
  /**
   * Stores all totalBar Prices information.
   */
  public prices: any = {};

  /**
   * custom attributes for an offer
   */
  public customFields?: any = {};

  /**
   * This function will insert lineItem to the parent.
   * @param parentInCardData
   * @param toBeAddedLineItem
   */
  public insertLineItemToParent(parentInCardData, toBeAddedLineItem) {
    let lineItemIndex;
    let lineItemListWithTheAddedItem = [];
    if (!parentInCardData.lineItems) {
      parentInCardData.lineItems = { records: [{}] };
    } else {
      lineItemListWithTheAddedItem = this.cloneDeep(
        parentInCardData.lineItems.records
      );
    }

    // Getting the index of the toBeAddedLineItem
    lineItemIndex = lineItemListWithTheAddedItem.findIndex(item => {
      return item.Id.value === toBeAddedLineItem.Id.value;
    });

    // If toBeAddedLineItem is not present in lineItems then add it and if present then replace it in lineItems
    if (lineItemIndex === -1) {
      lineItemListWithTheAddedItem.push(toBeAddedLineItem);
    } else {
      const itemToBeReplaced = lineItemListWithTheAddedItem[lineItemIndex];
      toBeAddedLineItem.actions = itemToBeReplaced.actions;
      toBeAddedLineItem.lineItems = itemToBeReplaced.lineItems;
      toBeAddedLineItem.childProducts = itemToBeReplaced.childProducts;
      toBeAddedLineItem.productGroups = itemToBeReplaced.productGroups;
      lineItemListWithTheAddedItem[lineItemIndex] = toBeAddedLineItem;
    }
    parentInCardData.lineItems.records = lineItemListWithTheAddedItem;

    // 1) If the to-be-added lineItem is an Optional product (Definition: minQuantity=0) and has defaultQuantity > 0,
    // that means it was a lineItem initially because defaultQuantity > 0,
    // but subsequently it was deleted and was removed from lineItems json array, but was added to as an Addon
    // in childProducts array.  Now that we are adding it back to lineItems, we also need to remove
    // the corresponding Addon in childProducts.
    // 2) We don't need to do this for Optional products (Definition: minQuantity=0) and have defaultQuantity = 0, because
    // they always have an Addon in the childProducts array.
    // 3) We also do not need to do this for Required products (Definition: minQuantity>0) as they cannot be
    // completely deleted and would never have a representation in the childProducts array.
    if (
      toBeAddedLineItem.minQuantity === 0 &&
      toBeAddedLineItem.defaultQuantity > 0
    ) {
      this.removeAddonFromParent(
        parentInCardData,
        toBeAddedLineItem.PricebookEntryId.value
      );
    }
    return parentInCardData;
  }

  /**
   * This function will update prices to the entire bundle recursively.
   * @param product
   * @param prices
   */
  public async updatePrices(product, prices) {
    if (product.records) {
      product.records.forEach(async obj => {
        prices = await this.replacePrices(obj, prices);
        if (obj.productGroups) {
          this.updatePrices(obj.productGroups, prices);
        }
        if (obj.lineItems) {
          this.updatePrices(obj.lineItems, prices);
        }
      });
    } else {
      prices = await this.replacePrices(product, prices);
      if (product.productGroups) {
        this.updatePrices(product.productGroups, prices);
      }
      if (product.lineItems) {
        this.updatePrices(product.lineItems, prices);
      }
    }
    return Promise.resolve(product);
  }

  /**
   * This function will remove lineItem from the parent.
   * @param parentInCardData
   * @param toBeRemovedLineItem
   */
  public removeLineItemFromParent(parentInCardData, toBeRemovedLineItem) {
    let lineItemList;
    let toBeRemovedProductId = "";
    const lineItemListWithoutTheRemovedItem = [];

    if (parentInCardData.lineItems) {
      lineItemList = this.cloneDeep(parentInCardData.lineItems.records);
      lineItemList.forEach(currentLineItem => {
        if (currentLineItem.Id.value !== toBeRemovedLineItem.Id.value) {
          lineItemListWithoutTheRemovedItem.push(currentLineItem);
        } else {
          toBeRemovedProductId = currentLineItem.Product2Id;
        }
      });
      // Delete the lineItem from parent by replacing the existing lineItems under the parent
      // by an array without the to-be-deleted lineItem
      parentInCardData.lineItems.records = lineItemListWithoutTheRemovedItem;
    }
    if (parentInCardData.childProducts) {
      const priceIndex = parentInCardData.childProducts.records.findIndex(
        childProduct =>
          (childProduct.PricebookEntry
            ? childProduct.PricebookEntry.Id
            : childProduct.Id.value) === toBeRemovedLineItem.PricebookEntry.Id
      );
      if (priceIndex === -1) {
        parentInCardData.childProducts.records.push(toBeRemovedLineItem);
      }
    } else {
      parentInCardData.childProducts = { records: [toBeRemovedLineItem] };
    }
    const quantityMapKey =
      SalesForceNamespace.namespace + "__InCartQuantityMap__c";
    const currentIncartQuantityMapValue =
      parentInCardData[quantityMapKey].value[toBeRemovedProductId];
    let quantityToBeRemoved = 1;
    if (
      toBeRemovedLineItem &&
      toBeRemovedLineItem.Quantity &&
      toBeRemovedLineItem.Quantity.value
    ) {
      quantityToBeRemoved = toBeRemovedLineItem.Quantity.value;
    }
    const remainingQuantity =
      currentIncartQuantityMapValue - quantityToBeRemoved;

    if (currentIncartQuantityMapValue && remainingQuantity > 0) {
      parentInCardData[quantityMapKey].value[
        toBeRemovedProductId
      ] = remainingQuantity;
    } else {
      delete parentInCardData[quantityMapKey].value[toBeRemovedProductId];
    }

    return parentInCardData;
  }

  /**
   * This function will remove child product from the parent.
   * @param parentInCardData
   * @param toBeRemovedAddonId
   */
  public removeAddonFromParent(parentInCardData, toBeRemovedAddonId) {
    let addonList;
    const addonListWithoutTheRemovedAddon = [];
    let i;

    if (parentInCardData.childProducts) {
      addonList = this.cloneDeep(parentInCardData.childProducts.records);
      for (i = 0; i < addonList.length; i++) {
        if (addonList[i].Id.value !== toBeRemovedAddonId) {
          addonListWithoutTheRemovedAddon.push(addonList[i]);
        }
      }
      parentInCardData.childProducts.records = addonListWithoutTheRemovedAddon;
    }
  }

  /**
   * This function will update prices to the given product.
   * Invoked by updatePrices() function
   * @param product
   * @param prices
   * * @return {any} - prices
   */
  private async replacePrices(product, prices, changePrices = true) {
    const priceIndex = prices.findIndex(
      lineItem => lineItem.Id.value === product.Id.value
    );
    if (priceIndex !== -1) {
      const keys = [
        SalesForceNamespace.namespace + "__EffectiveOneTimeTotal__c",
        SalesForceNamespace.namespace + "__EffectiveRecurringTotal__c",
        SalesForceNamespace.namespace + "__OneTimeCalculatedPrice__c",
        SalesForceNamespace.namespace + "__OneTimeCharge__c",
        SalesForceNamespace.namespace + "__OneTimeManualDiscount__c",
        SalesForceNamespace.namespace + "__OneTimeTotal__c",
        SalesForceNamespace.namespace + "__RecurringCalculatedPrice__c",
        SalesForceNamespace.namespace + "__RecurringCharge__c",
        SalesForceNamespace.namespace + "__RecurringManualDiscount__c",
        SalesForceNamespace.namespace + "__RecurringTotal__c",
        SalesForceNamespace.namespace + "__EffectiveQuantity__c",
        SalesForceNamespace.namespace + "__UsageUnitPrice__c"
      ];
      keys.forEach(key => {
        if (product[key] && product[key].actions) {
          product[key].value = prices[priceIndex][key]
            ? prices[priceIndex][key].value
            : product[key].value;
        } else {
          product[key] = prices[priceIndex] ? prices[priceIndex][key] : 0;
        }
      });
      if (changePrices) {
        prices.splice(priceIndex, 1);
      }
    }
    return prices;
  }

  /**
   * This function will perform deep cloning of an given object.
   * @param obj
   */
  private cloneDeep(obj) {
    return JSON.parse(JSON.stringify(obj));
  }
}
