declare const SF_NAMESPACE: string;

/**
 * This class returns the current package namespace, prefix for sobjects, fields and SF REST API prefix as follows:
 * 1. vlocity_cmt with namespace() (for use in vlocity_cmt.CpqAppHandler)
 * 2. vlocity_cmt__ with prefix() (for use in custom sobject or fields e.g. vlocity_cmt__OrderProvisioningStatus__c)
 * 3. /vlocity_cmt with restAPIPrefix() (for use in sf rest API - /vlocity_cmt/v1/proudcts)
 */
export class SalesForceNamespace {
  // Build script populates(replaces) the SF_NAMESPACE based on env properties
  private static internalPrefix: string = `${SF_NAMESPACE}__`;
  private static internalNamespace: string = SF_NAMESPACE;
  private static internalRestAPIPrefix: string = `/${SF_NAMESPACE}`;

  /**
   * This function returns the namespace that is being used in SDK.
   * @returns namespace.
   */
  public static get namespace(): string {
    return SalesForceNamespace.internalNamespace;
  }

  /**
   * This function provides a way to override the default or build time set up of namespace.
   * @param namespace set to be used in SDKs.
   */
  public static set namespace(customNamespace: string) {
    SalesForceNamespace.internalNamespace = customNamespace;
    if (!customNamespace) {
      SalesForceNamespace.internalPrefix = customNamespace;
      SalesForceNamespace.internalRestAPIPrefix = customNamespace;
    } else {
      SalesForceNamespace.internalPrefix = `${customNamespace}__`;
      SalesForceNamespace.internalRestAPIPrefix = `/${customNamespace}`;
    }
  }

  /**
   * This returns the namespace prefix for sobject and fields.
   * If namespace is defined and not an empty string, this will return {namespace}__. e.g. vlocity_cmt__
   * If namespace is empty, this will return an empty string.
   *
   * @example
   * ```typescript
   * `${SalesForceNamespace.prefix}OrderLineItemProvisioningStatus__c`
   * ```
   */
  public static get prefix(): string {
    return SalesForceNamespace.internalPrefix;
  }

  /**
   * This returns the SalesForce Rest URL prefix with the preceding /.
   * If namespace is defined and not an empty string, this will return /{namespace}. e.g. /vlocity_cmt
   * If namespace is empty, this will return an empty string.
   *
   * @example
   * ```typescript
   * `/services/apexrest${SalesForceNamespace.restAPIPrefix}/v2/carts/items`
   * ```
   */
  public static get restAPIPrefix(): string {
    return SalesForceNamespace.internalRestAPIPrefix;
  }
}
