import { HttpMethod } from "../../common";
import DataSourceBrowserDB from "../../common/DataSourceBrowserDB";
import { SalesForceNamespace } from "../../common/SalesForceNamespace";
import { Logger } from "../../logger";
import * as UrlUtil from "../../utils/url.util";
import { ApexRestDataSource, ApexRestInput } from "../apexrest";
import IDataSource from "../DataSource.interface";
import Delay from "../utils/delay";
import { DataRaptorInput } from "./";
export class DataRaptor implements IDataSource {
  // @TODO: Avoid using any
  private connection: any;
  private input: DataRaptorInput;
  private apexRestInput: ApexRestInput;
  private dataSourceDB: DataSourceBrowserDB;
  private inPlatformData?: Promise<Object>;

  constructor(
    connection: any,
    input: DataRaptorInput,
    dataSourceDB: DataSourceBrowserDB,
    inPlatformData?: Promise<Object>
  ) {
    this.connection = connection;
    this.input = input;
    this.input.retryCount =
      this.input.retryCount !== undefined
        ? this.input.retryCount
        : Delay.defaultRetryCount;
    this.input.timeoutInMillis =
      this.input.timeoutInMillis !== undefined
        ? this.input.timeoutInMillis
        : Delay.defaultTimeout;
    this.dataSourceDB = dataSourceDB;
    this.inPlatformData = inPlatformData;
  }

  /**
   * @param  {DataRaptorInput} input
   * @returns Promise
   */
  public async execute(): Promise<any> {
    Logger.log("DataSource call input", this.input);
    // @TODO:Implementation supports only "get" calls. DR extract may not work, support all DR types.

    try {
      if (typeof this.inPlatformData === "function") {
        const dataHandler: any = this.inPlatformData;
        const dataRaptorData: any = {
          type: "dataraptor",
          value: {
            bundleName: this.input.bundleName,
            inputMap: JSON.stringify(this.input.inputMap)
          }
        };
        const response = await dataHandler(JSON.stringify(dataRaptorData));
        return JSON.parse(response);
      } else if (this.inPlatformData) {
        return this.inPlatformData;
      }

      // @Todo:Implementation pending for Apex remote
      if (false) {
        // @TODO Implementation pending
        // Logger.log("In Dataraptor using ApexRemote", this.input);
        // const apexRemoteDataSource = new ApexRemoteDataSource(
        //   this.connection,
        //   this.apexRemoteInput
        // );
        // return await apexRemoteDataSource.execute();
      } else {
        Logger.log("In Dataraptor using ApexRest", this.input);
        const dataRaptorPath: ApexRestInput = new ApexRestInput({
          method: HttpMethod.GET,
          path: this.getDataraptorPath(
            this.input.bundleName,
            this.input.inputParamsString,
            this.input.inputMap
          ),
          retryCount: this.input.retryCount,
          timeoutInMillis: this.input.timeoutInMillis,
          cacheable: this.input.cacheable,
          cacheExpiration: this.input.cacheExpiration,
          reload: this.input.reload,
          cacheType: this.input.cacheType
        });
        const apexRestDataSource = new ApexRestDataSource(
          this.connection,
          dataRaptorPath,
          this.dataSourceDB
        );
        return await apexRestDataSource.execute();
      }
    } catch (error) {
      let errMsg =
        typeof error === "object" && error !== null
          ? JSON.stringify(error)
          : error;
      return Promise.reject(new Error(errMsg));
    }
  }

  /**
   * @param contextId {string} DataRaptor contextid
   * @param inputParamsString {string} input as string eg: "name=vlocity"
   * @param inputMap {object} eg: input as object eg: {name: "vlocity"}
   * @returns {string} path
   */
  private getDataraptorPath(
    contextId: string,
    inputParamsString: string,
    inputMap: object
  ) {
    let queryString = "";

    if (inputParamsString) {
      queryString = "?" + inputParamsString;
    } else if (inputMap) {
      queryString = UrlUtil.queryStringEncode(inputMap);
    }

    const dataraptorPath = `${
      SalesForceNamespace.restAPIPrefix
    }/v2/DataRaptor/${contextId}/${queryString}`;

    return dataraptorPath;
  }
}
