import { clear, del, get, keys, set, Store } from "idb-keyval";
import { CacheResult } from "../datasource/common/CacheResult";
import { isNode } from "../datasource/utils/isNode";
import { CacheType } from "./cacheType";
const namespace = "VlocitySDK";
const DEFAULT_CACHE_EXPIRATION = new Date(new Date().getTime() + 5 * 60000);
export default class DataSourceBrowserDB {
  public static isIndexedDBAvailable: boolean = isNode
    ? false
    : "indexedDB" in window;
  private storeName;
  private dbName;
  private db: Store;
  constructor(dbName, storeName) {
    this.dbName = dbName;
    this.storeName = namespace + "." + storeName;
  }
  /**
   * this method will create Indexed DB with given namespace
   */
  public createIndexedDBStore() {
    if (DataSourceBrowserDB.isIndexedDBAvailable) {
      this.db = new Store(this.dbName, this.storeName);
    }
  }
  /**
   * this method will return cached response for given key
   * @param key - the key where data is indexed
   * @param cacheType is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public async get(key, cacheType?): Promise<CacheResult> {
    if (isNode) {
      return Promise.reject();
    }
    if (!cacheType || cacheType === CacheType.INDEXEDDB) {
      if (DataSourceBrowserDB.isIndexedDBAvailable && this.db) {
        return <Promise<CacheResult>>get(key, this.db);
      } else {
        return <Promise<CacheResult>>(
          (localStorage.getItem(key)
            ? JSON.parse(localStorage.getItem(key))
            : null)
        );
      }
    } else if (cacheType === CacheType.LOCALSTORAGE) {
      return <Promise<CacheResult>>(
        (localStorage.getItem(key)
          ? JSON.parse(localStorage.getItem(key))
          : null)
      );
    } else if (cacheType === CacheType.SESSIONSTORAGE) {
      return <Promise<CacheResult>>(
        (sessionStorage.getItem(key)
          ? JSON.parse(sessionStorage.getItem(key))
          : null)
      );
    }
  }
  /**
   * This method will set the cache for given key
   * @param key - the key where data should be indexed
   * @param val - the value which should be stored in indexed DB
   * @param cacheType is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public async set(key, val, cacheType?) {
    if (isNode) {
      return Promise.reject();
    }
    if (!cacheType || cacheType === CacheType.INDEXEDDB) {
      if (DataSourceBrowserDB.isIndexedDBAvailable && this.db) {
        return set(key, val, this.db);
      } else {
        return localStorage.setItem(key, val);
      }
    } else if (cacheType === CacheType.LOCALSTORAGE) {
      return localStorage.setItem(key, val);
    } else if (cacheType === CacheType.SESSIONSTORAGE) {
      return sessionStorage.setItem(key, val);
    }
  }
  /**
   * This method will delete the cache for given key
   * @param key - the key using which data can be indexed
   * @param cacheType is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public async delete(key, cacheType?) {
    if (isNode) {
      return Promise.reject();
    }
    if (!cacheType || cacheType === CacheType.INDEXEDDB) {
      if (DataSourceBrowserDB.isIndexedDBAvailable && this.db) {
        return del(key, this.db);
      } else {
        return localStorage.removeItem(key);
      }
    } else if (cacheType === CacheType.LOCALSTORAGE) {
      return localStorage.removeItem(key);
    } else if (cacheType === CacheType.SESSIONSTORAGE) {
      return sessionStorage.removeItem(key);
    }
  }
  /**
   * This method will clear the indexed DB store
   */
  public async clear() {
    clear(this.db);
  }
  /**
   * This method will return all the keys for current Database and the store
   */
  public async keys() {
    if (!DataSourceBrowserDB.isIndexedDBAvailable) {
      return Promise.reject();
    }
    return keys(this.db);
  }

  /**
   * getCachedResults will return the cached Api reponse for given Indexed DB key within cache duration
   * @param iDBKey - the key used to get the cached data, if it is within cacheExpiration time
   *
   */
  public async getCachedResults(iDBKey) {
    let results: CacheResult;
    if (DataSourceBrowserDB.isIndexedDBAvailable) {
      results = await this.get(iDBKey);
    } else {
      return Promise.reject(results);
    }
    if (results && results.cacheExpiration.getTime() > Date.now()) {
      return Promise.resolve(JSON.parse(results.result));
    }
    return Promise.reject();
  }
  /**
   * setResultsCache sets the result to the Indexed DB
   * @param iDBKey is the Indexed DB key to cache data
   * @param result is the data which needs to be cached
   * @param cacheExpiration is the duration for which cache should be accessible
   */
  public async setResultsCache(iDBKey, result, cacheExpiration) {
    let results = new CacheResult();
    results.result = JSON.stringify(result);
    results.cacheExpiration = cacheExpiration
      ? cacheExpiration
      : result.cacheExpiration;
    await this.set(iDBKey, results);
  }
  public async deleteDatabase() {
    const request = indexedDB.deleteDatabase(this.dbName);
    return new Promise((resolve, reject) => {
      request.onerror = err => {
        reject(err);
      };
      request.onsuccess = () => {
        resolve();
      };
    });
  }
  public getCacheableValue(cacheable, url): boolean {
    let isCacheEnabled = true;
    if (url && url.split("?")[1]) {
      const queryStringpParams = url.split("?")[1].toLowerCase();
      const useCacheValue = queryStringpParams.includes("usecache=")
        ? queryStringpParams.split("usecache=")[1].split("&")[0]
        : null;
      isCacheEnabled = useCacheValue === null ? true : useCacheValue === "true";
    }
    return cacheable && isCacheEnabled;
  }
  /**
   * Method to fetch cache if reload is true
   * @param key - is the Indexed DB key to cache data
   * @param reload - value set to specify if data should be fetched from cache.
   * @param cacheType is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public async getDataFromCache(key, reload, cacheType?) {
    if (reload || isNode) {
      return Promise.reject();
    }
    if (
      (!cacheType || cacheType === CacheType.INDEXEDDB) &&
      DataSourceBrowserDB.isIndexedDBAvailable
    ) {
      if (this.db) {
        return this.getCachedResults(key);
      } else {
        return this.getCachedResultsLocalStorage(key);
      }
    } else if (cacheType === CacheType.LOCALSTORAGE) {
      return this.getCachedResultsLocalStorage(key);
    } else if (cacheType === CacheType.SESSIONSTORAGE) {
      return this.getCachedResultsSessionStorage(key);
    }
    return Promise.reject();
  }
  /**
   * setCache sets the result to the cache
   * @param key is the key to cache data
   * @param result is the data which needs to be cached
   * @param cacheExpiration is the duration for which cache should be accessible
   * @param cacheType is a optional parameter. It is the type of cache to be used. It will be of the type CacheType Enum. If not specified it will be indexed db
   */
  public async setCache(key, result, cacheExpiration, cacheType?) {
    if (isNode) {
      return Promise.reject();
    }
    if (!cacheType || cacheType === CacheType.INDEXEDDB) {
      if (this.db) {
        return this.setResultsCache(key, result, cacheExpiration);
      } else {
        return this.setResultsCacheLocalStorage(key, result, cacheExpiration);
      }
    } else if (cacheType === CacheType.LOCALSTORAGE) {
      return this.setResultsCacheLocalStorage(key, result, cacheExpiration);
    } else if (cacheType === CacheType.SESSIONSTORAGE) {
      return this.setResultsCacheSessionStorage(key, result, cacheExpiration);
    }
  }

  /**
   * getCachedResultsLocalStorage will return the cached Api reponse for given Local Storage key within cache duration
   * @param Key - the key used to get the cached data, if it is within cacheExpiration time
   *
   */
  public async getCachedResultsLocalStorage(Key) {
    let results: CacheResult;
    results = localStorage.getItem(Key)
      ? JSON.parse(localStorage.getItem(Key))
      : null;
    if (results && new Date(results.cacheExpiration).getTime() > Date.now()) {
      return Promise.resolve(JSON.parse(results.result));
    }
    return Promise.reject();
  }
  /**
   * setResultsCacheLocalStorage sets the result to the Local Storage
   * @param key is the Local Storage key to cache data
   * @param result is the data which needs to be cached
   * @param cacheExpiration is the duration for which cache should be accessible
   */
  public async setResultsCacheLocalStorage(key, result, cacheExpiration) {
    let results = new CacheResult();
    results.result = JSON.stringify(result);
    results.cacheExpiration = cacheExpiration
      ? cacheExpiration
      : result.cacheExpiration;
    await localStorage.setItem(key, JSON.stringify(results));
  }
  /**
   * getCachedResultsSessiomStorage will return the cached Api reponse for given Session Storage key within cache duration
   * @param Key - the key used to get the cached data, if it is within cacheExpiration time
   *
   */
  public async getCachedResultsSessionStorage(Key) {
    let results: CacheResult;
    results = sessionStorage.getItem(Key)
      ? JSON.parse(sessionStorage.getItem(Key))
      : null;
    if (results && new Date(results.cacheExpiration).getTime() > Date.now()) {
      return Promise.resolve(JSON.parse(results.result));
    }
    return Promise.reject();
  }
  /**
   * setResultsCacheSessionStorage sets the result to the session Storage
   * @param key is the session Storage key to cache data
   * @param result is the data which needs to be cached
   * @param cacheExpiration is the duration for which cache should be accessible
   */
  public async setResultsCacheSessionStorage(key, result, cacheExpiration) {
    let results = new CacheResult();
    results.result = JSON.stringify(result);
    results.cacheExpiration = cacheExpiration
      ? cacheExpiration
      : result.cacheExpiration;
    await sessionStorage.setItem(key, JSON.stringify(results));
  }
}
