import { objectMap } from "../../utils";
/**
 * Class that defines time lists.
 */
export class TimeList {
  /**
   * This function parse the TimeLists details.
   * @param timeListsResponse
   * @return {TimeList}
   */
  public static parse(timeListsResponse: any, customFields: any[]): TimeList {
    const timeList = new TimeList();
    timeList.messages = timeListsResponse.messages;
    if (timeListsResponse.records) {
      timeListsResponse.records.forEach(timelist => {
        const fetchedtimeLists = objectMap(
          timelist.listvalues,
          [
            "actions",
            "fields.valuekey",
            "fields.label",
            "fields.valuekey#value"
          ].concat(customFields)
        );
        if (timelist.listkey === "TimePlans" && fetchedtimeLists) {
          timeList.timePlans = fetchedtimeLists;
        } else if (fetchedtimeLists) {
          timeList.timePolicies = fetchedtimeLists;
        }
      });
    }
    return timeList;
  }

  /**
   * Time Plans.
   */
  public timePlans: any = [];

  /**
   * Time Policies.
   */
  public timePolicies: any = [];

  /**
   * messages.
   */
  public messages: any = [];
}
