import { SalesForceNamespace } from "../../common";
import { CartItems } from "../cart";
import { CrossActionsService } from "./CrossActionsService";
export class ItemDeleted {
  public static handleCrossAction(
    deletedItemIds,
    record,
    child,
    rootBundle?: any
  ): any {
    const DELETE_ACTION = "delete";
    if (!(child && child.deletedItemIds)) {
      return;
    }

    deletedItemIds.forEach(idObject => {
      if (
        child.deletedItemIds &&
        child.deletedItemIds.indexOf(idObject.Id) > -1
      ) {
        // Same addon can be used for adding multiple deleted lineitems eg: when cloned line items are deleted
        delete child.deletedItemIds;
        // check passing extra record here
        rootBundle = this.processDeleteAction(
          DELETE_ACTION,
          idObject,
          false,
          child,
          rootBundle
        );
      }
    });
    return rootBundle;
  }

  public static processDeleteAction(
    type,
    record,
    isNewRootItem,
    addonProduct,
    parent
  ) {
    if (!type || !record) {
      return parent;
    }
    const data = {
      itemId: record.Id,
      RootItemId:
        record[SalesForceNamespace.namespace + "__RootItemId__c"] &&
        record[SalesForceNamespace.namespace + "__RootItemId__c"].value,
      AssetReferenceId:
        record[SalesForceNamespace.namespace + "__AssetReferenceId__c"] &&
        record[SalesForceNamespace.namespace + "__AssetReferenceId__c"].value,
      addonProduct
    };
    if (data.itemId && parent && data.itemId === parent.Id.value) {
      if (
        parent.Id.value ===
          parent[SalesForceNamespace.namespace + "__RootItemId__c"].value ||
        parent[SalesForceNamespace.namespace + "__RootItemId__c"].value ===
          parent[SalesForceNamespace.namespace + "__AssetReferenceId__c"]
            .value ||
        (data.RootItemId &&
          data.AssetReferenceId &&
          data.RootItemId === data.AssetReferenceId)
      ) {
        // todo Handles root item
        // $scope.$parent.$emit("vlocity.cpq.cart.removerecords", $scope.$parent);
        if (!parent.itemsToBeDeleted) {
          parent.itemsToBeDeleted = [];
        }
        parent.itemsToBeDeleted.push(parent.Id.value);
      }
    } else if (parent) {
      // Handles non-root item

      let lineItemToBeDeleted = null;
      if (parent.lineItems) {
        const lineItemDetails = CartItems.findAndReplaceLineItemInCart(
          parent,
          data.itemId,
          null,
          true
        );

        if (lineItemDetails) {
          lineItemToBeDeleted = CrossActionsService.searchLineItem(
            parent,
            data.itemId
          ) || null; 
          if(lineItemToBeDeleted)  {   
              this.deleteLineItem(
                lineItemDetails.lineItem,
                lineItemToBeDeleted,
                null,
                false
              );
          }
        }
      }

      if (!lineItemToBeDeleted && parent.productGroups) {
        const lineItemDetails = CartItems.findAndReplaceLineItemInCart(
          parent,
          data.itemId,
          null,
          true
        );
        if (lineItemDetails) {
          lineItemToBeDeleted =
            CartItems.findAndReplaceLineItem(
              parent,
              data.itemId
            ) || null;
            if(lineItemToBeDeleted) {
              this.deleteLineItem(
                lineItemDetails.lineItem,
                lineItemToBeDeleted,
                data.addonProduct,
                false
              );
            }       
        }
      }
    }
    return parent;
  }

  public static changeLineItemCountInCardinalityMap(
    cardinalityMap,
    product2Id,
    changeQty
  ) {
    const productCountInMap = cardinalityMap[product2Id];
    let productCountAfterChange;
    if (productCountInMap) {
      productCountAfterChange = productCountInMap + changeQty;
      if (productCountAfterChange > 0) {
        cardinalityMap[product2Id] = productCountAfterChange;
      } else {
        delete cardinalityMap[product2Id];
      }
    }
  }

  public static deleteLineItem(
    parentInCardData,
    toBeRemovedLineItem,
    addonProductFromAPI,
    cardinalityMapAlreadyUpdated,
    actionStatusActive?: boolean
  ) {
    const cartitemObj = new CartItems();
    const toBeRemovedLineItemId = toBeRemovedLineItem.Id.value;
    const supplementAction =
      SalesForceNamespace.namespace + "__SupplementalAction__c";
    let parentInCardDataCardinalityMap;
    let toBeRemovedLineItemProduct2Id;
    let toBeRemovedLineItemQuantity;
    let addonProductIsLastInstanceUnderParent;
    let actionStatusDisconnected;

    if (!cardinalityMapAlreadyUpdated) {
      parentInCardDataCardinalityMap = parentInCardData
        ? parentInCardData[
            SalesForceNamespace.namespace + "__InCartQuantityMap__c"
          ].value
        : null;
      toBeRemovedLineItemProduct2Id =
        toBeRemovedLineItem.PricebookEntry.Product2.Id;
      toBeRemovedLineItemQuantity = toBeRemovedLineItem.Quantity.value;
      if (parentInCardDataCardinalityMap) {
        this.changeLineItemCountInCardinalityMap(
          parentInCardDataCardinalityMap,
          toBeRemovedLineItemProduct2Id,
          toBeRemovedLineItemQuantity * -1
        );
      }
    }

    if (!actionStatusActive) {
      parentInCardData = CrossActionsService.removeLineItemFromParent(
        parentInCardData,
        toBeRemovedLineItemId
      );
    }

    // 1) Only Optional products (Definition: minQuantity=0) with defaultQuantity > 0 would need to be put into childProducts
    // if there is NONE OTHER instance of it under the parent, such that it would show up with "Add to Cart" button.
    // 2) For Optional products (Definition: minQuantity=0) with defaultQuantity = 0,
    // they are always in childProducts and addonProduct returned from the deleteAPI would be null.
    // 3) Required products (Definition: minQuantity>0) would never have a representation in the childProducts array
    // and addonProduct returned from the deleteAPI would be null.

    // For Case 1: Optional products (Definition: minQuantity=0) with defaultQuantity > 0
    if (
      addonProductFromAPI &&
      addonProductFromAPI.minQuantity === 0 &&
      addonProductFromAPI.defaultQuantity > 0
    ) {
      // Only do the following to remove the childProduct representation of the lineItem if parent is NOT collapsable
      if (
        !parentInCardData.actions ||
        (parentInCardData.actions && !parentInCardData.actions.getproducts)
      ) {
        // Check to see if addonProduct is the last instance under the parent
        addonProductIsLastInstanceUnderParent = true;
        for (const lineItemUnderParent of parentInCardData.lineItems.records) {
          actionStatusDisconnected =
            (lineItemUnderParent[
              SalesForceNamespace.namespace + "__Action__c"
            ] &&
              lineItemUnderParent[
                SalesForceNamespace.namespace + "__Action__c"
              ].value.toLowerCase() === "disconnect") ||
            (lineItemUnderParent[supplementAction] &&
              lineItemUnderParent[supplementAction].value &&
              lineItemUnderParent[supplementAction].value.toLowerCase() ===
                "cancel")
              ? true
              : false;

          // Only lineItems of parent that are not "Deleted" in its action status would be counted as actual instance of the optional addOn
          if (
            !actionStatusDisconnected &&
            lineItemUnderParent.PricebookEntry.Product2.Id ===
              addonProductFromAPI.Product2.Id
          ) {
            addonProductIsLastInstanceUnderParent = false;
          }
        }

        // Only insert addonProduct into childProducts if it is the last instance of its product2 type under the parent,
        // because this is Case 1: Optional products (Definition: minQuantity=0) with defaultQuantity > 0,
        // as such, ONLY 1 addon needs to be in childProducts

        if (addonProductIsLastInstanceUnderParent) {
          if (!parentInCardData.childProducts) {
            parentInCardData.childProducts = {};
            parentInCardData.childProducts.records = [];
          }

          parentInCardData.childProducts.records.push(addonProductFromAPI);
        }
      }

      // For Case 2: Optional products (Definition: minQuantity=0) with defaultQuantity = 0
    } else if (
      addonProductFromAPI &&
      addonProductFromAPI.minQuantity === 0 &&
      addonProductFromAPI.defaultQuantity === 0
    ) {
      if (parentInCardData.actions && parentInCardData.actions.getproducts) {
        // remove childProduct from parent if the parent is Collapsable
        cartitemObj.removeAddonFromParent(
          parentInCardData,
          toBeRemovedLineItem.PricebookEntryId.value
        );
      } else {
        // Replace it by the (updated) one from API
        if(parentInCardData?.childProducts?.records){
          for(let childProdIndex = 0; childProdIndex < parentInCardData.childProducts.records.length; childProdIndex++){
            const childProduct = parentInCardData.childProducts.records[childProdIndex];
            if (childProduct.Product2.Id === addonProductFromAPI.Product2.Id) {
              parentInCardData.childProducts.records[childProdIndex] = addonProductFromAPI;
              break;
            }
          }
        }
      }
    }
  }
}
