import { HttpMethod } from "../../common";
import { BaseDataSourceInput } from "../common";
/**
 * Class that defines input for ApexRestInput  .
 * @extends BaseDataSourceInput
 */
export class ApexRestInput extends BaseDataSourceInput {
  /**
   * URL path of the Apex REST service
   */
  public path: string;
  /**
   * HttpMethod of the Apex REST Request
   */
  public method: HttpMethod;
  /**
   * Request body of the Apex REST Request
   */
  public body?: any;
  /**
   * Holds headers and other meta data for the request.
   */
  public options?: any;
  constructor(apexRestInput) {
    super(
      apexRestInput.cacheable,
      apexRestInput.cacheExpiration,
      apexRestInput.retryCount,
      apexRestInput.timeoutInMillis,
      apexRestInput.reload,
      apexRestInput.cacheType
    );
    this.path = apexRestInput.path;
    this.method = apexRestInput.method;
    this.body = apexRestInput.body;
    this.options = apexRestInput.options;
  }
  /**
   * This function generates a unique digest value for this input.
   * Subclass needs to provide the unique data to be digested to ensure the uniqueness.
   * @param data unique data to be digested.
   * @returns Promise filled with hex string representation of the digested value. This hex string can be used for comparison and display.
   */
  public digest(): Promise<string> {
    return super.digestData(
      this.path,
      this.method,
      this.body,
      this.options ? JSON.stringify(this.options) : ""
    );
  }
}
