import { CartItems } from "../cart";
import { CrossActionsService } from "./CrossActionsService";
import { ItemAdded } from "./ItemAdded";
import { ItemDeleted } from "./ItemDeleted";
import { ItemUpdated } from "./ItemUpdated";
/**
 * Class that defines each cartItem.
 */
export class CrossActions {
  public static processCrossActions(
    clientActionsObject: any,
    cartItems: any,
    parent: any,
    rootItem: any
  ) {
    let deletedItemIds = [];
    if (!clientActionsObject) {
      return;
    }

    cartItems.crossActionMessages = [];
    // if both itemadded and itemdeleted actions exist, this is a case of auto-replace
    if (clientActionsObject.itemadded && clientActionsObject.itemdeleted) {
      const autoReplaceMsg = {
        message:
          "Auto-replace rule has been triggered.  It will auto-add one item to replace another item which would be auto-removed from the cart"
      };
      cartItems.crossActionMessages = cartItems.crossActionMessages.concat(
        autoReplaceMsg
      );
    }
    for (const actionType of Object.keys(clientActionsObject)) {
      const clientAction = clientActionsObject[actionType].client;
      const records = clientAction && clientAction.records;
      if (actionType === "itemdeleted") {
        // @TODO: use records.
        deletedItemIds = clientAction.params.items;
      }
      if (records) {
        if (
          clientActionsObject[actionType].client &&
          clientActionsObject[actionType].client.params.items
        ) {
          cartItems.crossActionMessages = cartItems.crossActionMessages.concat(
            clientActionsObject[actionType].client.params.items
          );
        }
        // Handle the multiple records for actions.
        // Eg: addtocart action can have 2 records belonging to 2 different root items
        records.forEach(record => {
          let itemChildRecords = [];
          let childRecords = [];
          switch (actionType) {
            case "itemadded":
              // If the cross action updated line item is not found in the current line item which updated the attribute.
              // Then it belongs to some other bundle in the cart.
              // We find that bundle and then process the cross action
              const addedRootItem = CartItems.findRootFromGivenLineItem(
                cartItems.records,
                record.Id.value
              );
              let addedCartItemIndex = 0;
              if (addedRootItem) {
                addedCartItemIndex = addedRootItem.index;
                rootItem = addedRootItem.lineItem;
              }
              // line item added
              const updatedCartItem = ItemAdded.handleCrossAction(
                { records: record },
                parent,
                rootItem
              );
              cartItems.records[addedCartItemIndex] = updatedCartItem;
              break;
            case "rootitemadded":
              cartItems.records.unshift(record);
              break;
            case "itemdeleted":
              // Find the deleted item PCR from childProducts or from productGroups.
              // Basically if the returned PCR(which replaces deleted item) is under Product group, it contains
              // parent line item, product group and then PCR. Nearest real parent is returned.
              itemChildRecords =
                (record.childProducts && record.childProducts.records) ||
                (record.productGroups && record.productGroups.records);

              if (!record.isRootRecord && itemChildRecords) {
                itemChildRecords.forEach(child => {
                  if (child.isVirtualItem) {
                    childRecords = CrossActionsService.findChildItemInProductGroups(
                      child
                    );
                  } else {
                    // if we dont have virtual item then also we need to delete line items.
                    ItemDeleted.handleCrossAction(
                      deletedItemIds,
                      record,
                      child,
                      rootItem
                    );
                  }
                  if (childRecords) {
                    childRecords.forEach(childRecord => {
                      ItemDeleted.handleCrossAction(
                        deletedItemIds,
                        child,
                        childRecord,
                        rootItem
                      );
                    });
                  }
                  if (
                    rootItem.itemsToBeDeleted &&
                    rootItem.itemsToBeDeleted.length
                  ) {
                    rootItem.itemsToBeDeleted.forEach(cartItemId => {
                      // Root item deleted
                      cartItems = this.deleteCartItem(cartItems, cartItemId);
                    });
                  }
                  const cartItemIndex = cartItems.records.findIndex(
                    cartItem => cartItem.Id.value === rootItem.Id.value
                  );
                  if (cartItemIndex !== -1) {
                    cartItems.records[cartItemIndex] = rootItem;
                  }
                });
              } else {
                // Root item deleted
                cartItems = this.deleteCartItem(cartItems, record.Id);
              }
              break;
            case "itemupdated":
              // If the cross action updated line item is not found in the current line item which updated the attribute.
              // Then it belongs to some other bundle in the cart.
              // We find that bundle and then process the cross action
              const updatedRootItem = CartItems.findRootFromGivenLineItem(
                cartItems.records,
                record.Id.value
              );
              let updatedCartItemIndex = 0;
              if (updatedRootItem) {
                updatedCartItemIndex = updatedRootItem.index;
                rootItem = updatedRootItem.lineItem;
              }
              const itemUpdatedCartItem = ItemUpdated.handleCrossAction(
                { records: record },
                parent,
                rootItem
              );
              cartItems.records[updatedCartItemIndex] = itemUpdatedCartItem;
              break;
            default:
              break;
          }
        });
      }
    }
    return cartItems;
  }

  public static deleteCartItem(cartItems, id) {
    const currentCartItemIndex = cartItems.records.findIndex(
      cartItem => cartItem.Id.value === id
    );
    if (currentCartItemIndex !== -1) {
      cartItems.records.splice(currentCartItemIndex, 1);
    }
    return cartItems;
  }
}
