import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { B2BInput } from "../common/B2BInput";
/**
 * Class that defines input to delete cart Item from the cart.
 * @extends B2BInput
 */
export class GetCartItemsInput extends B2BInput {
  public fieldsToInclude = [
    "pagesize",
    "offsetSize",
    "validate",
    "price",
    "translation",
    "hierarchy",
    "lastRecordId",
    "includeIneligible",
    "query",
    "id",
    "filter",
    "priceDetailsFields",
    "fields",
    "includeAttachment"
  ];
  /**
   * The cart Id to be used for getting a list of line items.
   * It may be the regular cart id or the anonymous cart id.
   */
  public cartId?: string = "";

  /**
   * The hierarchy level to be retrieved.
   * For only the current level of products, hierarchy will be 0.
   * For retrieving current level of products with children, hierarchy will be 1.
   */
  public hierarchy?: number;

  /**
   * When doing pagination, provide the last record ID that was previously return.
   * If last record ID is not provided, get products will start from the beginning.
   */
  public lastRecordId?: string;

  /**
   * Search Text
   */
  public query?: string;

  /**
   * List of comma separated itemIds to return only the corresponding cart Items
   */
  public id?: string;

  /**
   * Filter field values.
   */
  public filter?: string;

  /**
   * Price Detail fields.
   */
  public priceDetailsFields?: string;

  /**
   * The offset to start the product list from.
   * If we are using pagesize of 20, the offset for next page will be 21.
   */
  public offsetSize?: number;

  /**
   * The number of top level products or offers to be returned.
   * Default is 20.
   */
  public pagesize?: number;

  /**
   * List of fields to be returned.
   * Supported Product2 and Pricebook fields.
   */
  public fields?: string;

  /**
   * Flag to indicate if validation rules should be run.
   */
  public validate: boolean = false;

  /**
   * Flag to indicate if pricing rules should be run.
   */
  public price: boolean = false;

  /**
   * Whether product attachments are returned.
   */
  public includeAttachment: boolean;

  /**
   * Remote class to handle get products.
   */
  public remoteClass: string = "CpqAppHandler";

  /**
   * Remote method in the above Remote Class to handle get cart items.
   */
  public remoteMethod: string = "getCartsItems";

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    if (!this.cartId) {
      throw new Error("GetCartItemInput::getAPIPath() must have cartId.");
    }
    return (
      `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${this.cartId}/items` +
      this.getEncodedQueryString()
    );
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(cartItems?: any): any {
    const requestBody: any = {};
    return requestBody;
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};
    this.fieldsToInclude.forEach(eachField => {
      if (this[eachField]) {
        inputMap[eachField] = this[eachField];
      }
    });

    if (!this.validate) {
      inputMap.validate = this.validate;
    }

    if (!this.price) {
      inputMap.price = this.price;
    }

    return inputMap;
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      this.fieldsToInclude
    );

    return encodedQueryString;
  }
}
