import "whatwg-fetch";
import { HttpMethod } from "../../common";
import DataSourceBrowserDB from "../../common/DataSourceBrowserDB";
import { Logger } from "../../logger";
import { BaseDataSourceInput } from "../common";
import IDataSource from "../DataSource.interface";
import ResponseError from "../ResponseError";
import Delay from "../utils/delay";
import { isNode } from "../utils/isNode";
import { RestInput } from "./RestInput";

export class RestDataSource implements IDataSource {
  private input: RestInput;
  private dataSourceDB: DataSourceBrowserDB;
  private inPlatformData?: Promise<Object>;
  constructor(
    input: RestInput,
    dataSourceDB: DataSourceBrowserDB,
    inPlatformData?: Promise<Object>
  ) {
    this.input = input;
    this.input.retryCount =
      this.input.retryCount !== undefined
        ? this.input.retryCount
        : Delay.defaultRetryCount;
    this.input.timeoutInMillis =
      this.input.timeoutInMillis !== undefined
        ? this.input.timeoutInMillis
        : Delay.defaultTimeout;
    this.dataSourceDB = dataSourceDB;
    this.inPlatformData = inPlatformData;
  }
  /**
   * Executes the Rest
   * @returns Promise
   */
  public async execute(): Promise<any> {
    try {
      Logger.log("Rest ", this.input);
      if (typeof this.inPlatformData === "function") {
        const dataHandler: any = this.inPlatformData;
        const restReqData: any = {
          type: "rest",
          value: {
            methodType: this.input.options.method,
            headers: this.input.options.headers,
            endpoint: this.input.url,
            data: this.input.options.body,
            namedCredential: this.input.options.credentials
          }
        };
        const response = await dataHandler(JSON.stringify(restReqData));
        return response;
      } else if (this.inPlatformData) {
        return this.inPlatformData;
      } else if (!isNode && this.input instanceof BaseDataSourceInput) {
        const iDBKey = await this.input.digest();
        return this.dataSourceDB
          .getDataFromCache(iDBKey, this.input.reload, this.input.cacheType)
          .catch(() => {
            return this.Rest(this.input)
              .then(async result => {
                (await this.dataSourceDB.getCacheableValue(
                  this.input.cacheable,
                  this.input.url
                )) &&
                  this.input.options.method === HttpMethod.GET &&
                  (await this.dataSourceDB.setCache(
                    iDBKey,
                    result,
                    this.input.cacheExpiration,
                    this.input.cacheType
                  ));
                return result;
              })
              .catch(error => {
                return error;
              });
          });
      } else {
        return this.Rest(this.input);
      }
    } catch (error) {
      let errMsg =
        typeof error === "object" && error !== null
          ? JSON.stringify(error)
          : error;
      return Promise.reject(new Error(errMsg));
    }
  }
  private async Rest(input: RestInput): Promise<any> {
    // @TODO implement default request options and merge with custom options
    let timeout;
    try {
      // Start the timer
      timeout = setTimeout(() => {
        return Promise.reject(new Error("Request timed out"));
      }, this.input.timeoutInMillis);

      const response = await fetch(this.input.url, this.input.options);
      // Clear the timeout after getting the response
      clearTimeout(timeout);
      if (!response.ok) {
        Logger.info("Rest call failed", response);
        return Promise.reject(
          new ResponseError(response.statusText, this.input.url, response)
        );
      }
      if (response.status === 204) {
        // Handling 204 (No Content).
        return await {};
      }
      return await response.json();
    } catch (error) {
      // When retry count is 0 or status code is 4xx stop retry
      if (error.status === 404 && this.input.retryCount !== 0) {
        --this.input.retryCount;
        await Delay.addRetryDelay();
        return await this.execute();
      } else {
        Logger.info("Rest call error", error);
        await error.response.json().then(errorJson => {
          error.errorJson = errorJson;
          return Promise.reject(error);
        });
      }
    }
  }
}
