import { queryStringEncode } from "../../utils";
import { B2BInput } from "../common/B2BInput";
/**
 * Class that defines input to update lineItem.
 * @extends B2BInput
 */
export class UpdateCartLineItemInput extends B2BInput {
  /**
   * lineItemId as part of the POST body of the API for updating lineItem.
   * lineItemId - the offer Id which needs to be updated to the cart.
   */
  public lineItemId: string;

  /**
   * parentId as part of the POST body of the API for updating lineItem.
   * Optional when updating parent bundle product.
   */
  public parentId?: string;

  /**
   * rootBundleId as part of the POST body of the API for updating lineItem.
   */
  public rootBundleId: string;

  /**
   * input = { action: 'updateQuantity', data : {'value': 2}}
   */
  public lineItemDetails: any = {};

  /**
   * Line item inside the anonymous cart to be updated.
   */
  public itemObject: { [key: string]: any };

  /**
   * Parent inside the anonymous cart to be updated.
   */
  public parentObject?: { [key: string]: any };

  /**
   * Indicator if user intends to add product selected to the anonymous shopping cart.
   */
  public commitToShoppingCart: boolean = false;

  /**
   * Indicator if user intends to enable rapid attribute to the anonymous shopping cart.
   */
  public isRapidAttributeEnabled: boolean = false;

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    return "";
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(cartItems?: any): any {
    if (!this.actionObj) {
      throw new Error("UpdateCartLineItemInput must have actionObj.");
    }
    if (!this.lineItemId) {
      throw new Error("UpdateCartLineItemInput must have lineItemId.");
    }
    if (!this.rootBundleId) {
      throw new Error("UpdateCartLineItemInput must have rootBundleId.");
    }
    if (!this.lineItemDetails) {
      throw new Error("UpdateCartLineItemInput must have lineItemDetails.");
    }
    const currentCartItem = cartItems.records.find(
      cartItem => cartItem.Id.value === this.rootBundleId
    );
    const itemObject = this.findLineItem(
      this.cloneDeep(currentCartItem),
      this.lineItemId
    );
    if (this.parentId) {
      this.parentObject = this.findLineItem(
        this.cloneDeep(currentCartItem),
        this.parentId
      );
    }
    this.itemObject = this.processLineItemDetails(itemObject);
    return this.actionObj;
  }

  /**
   * Function that updates details of an lineItem.
   * Eg: Quantity, attribute values.
   * @param lineItem - product to be udpated
   * @returns {any} - updated lineItem
   */
  public processLineItemDetails(lineItem) {
    if (this.lineItemDetails.action === "updateQuantity") {
      lineItem.Quantity.value = this.lineItemDetails.data.value;
    } else if (this.lineItemDetails.action === "updateAttribute") {
      const attributesHierarchyArray = this.lineItemDetails.data.attributeHierarchyPath.split(
        "<"
      );
      lineItem.attributeCategories.records.forEach(category => {
        if (category.id === attributesHierarchyArray[0]) {
          category.productAttributes.records.forEach(attributeObj => {
            if (attributeObj.attributeId === attributesHierarchyArray[1]) {
              attributeObj.userValues = this.lineItemDetails.data.value;
            }
          });
        }
      });
    }
    return lineItem;
  }

  /**
   * Function that constructs the input map object containing all query parameters for API.
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};
    return inputMap;
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
