const retryDelay: number = 500;

export default class Delay {
  public static defaultRetryCount: number = 3;
  public static defaultTimeout: number = 10000;

  public static setDefaultRetryCount(retryCount: number) {
    this.defaultRetryCount = retryCount;
  }

  /**
   * addRetryDelay - Add delay before next retry
   * @returns Promise
   */

  public static addRetryDelay() {
    return new Promise(resolve => {
      //constant retry Delay
      setTimeout(resolve, retryDelay);
    });
  }

  public static setDefaultTimeout(timeout) {
    this.defaultTimeout = timeout;
  }
}
