import { Logger } from "../logger";

/**
 * Function that find product group for given hierarchial path.
 * @param hierarchyPath - list of id's
 * @param productGroups - list of product groups
 * @returns {any}
 */

export function findProductGroup(hierarchyPath: any, productGroups: any): any {
  try {
    for (const productGroup of productGroups) {
      if (productGroup.Id === hierarchyPath[0]) {
        if (productGroup.childProducts) {
          return productGroup;
        } else if (productGroup.productGroups) {
          return findProductGroup(
            hierarchyPath.slice(1),
            productGroup.productGroups
          );
        }
      }
    }
  } catch (e) {
    Logger.info("@@@ here in catch", e);
    return null;
  }
}

/**
 * Function which sorts given array
 * @param list - Array which needed to be sorted. It can be Array of string or numbers or objects.
 * @param sortKey - Optional. Pass a sort by key.
 * @param orderByDesc - Optional. This is to specify order. Pass true for descending.
 */
export function sortItems(
  list: any[],
  sortKey?: string,
  orderByDesc?: boolean
): any[] {
  try {
    let sortedList = [];
    if (sortKey) {
      sortedList = list.sort((a, b) =>
        a[sortKey].toString().localeCompare(b[sortKey].toString())
      );
    } else {
      sortedList = list.sort();
    }
    return orderByDesc ? sortedList.reverse() : sortedList;
  } catch (e) {
    return list;
  }
}
