import { objectMap, sortItems } from "../../utils";
/**
 * Class that defines Product Attributes.
 */
export class AttributeCategory {
  /**
   * This function parse the product attributes.
   * @param category
   * @param parentId - optional
   * @return {AttributeCategory}
   */
  public static parse(
    category: any,
    input: any,
    parentId?: string
  ): AttributeCategory {
    const attributeCategory = new AttributeCategory();
    attributeCategory.code = category.Code__c;
    attributeCategory.name = category.Name;
    attributeCategory.id = category.id;
    const sortedAttributes = sortItems(
      category.productAttributes.records,
      "displaySequence"
    );
    if (sortedAttributes) {
      sortedAttributes.forEach(productAttributes => {
        const obj = objectMap(
          [productAttributes],
          [
            "attributeId",
            "disabled",
            "label",
            "messages",
            "readonly",
            "required",
            "userValues",
            "hidden",
            "multiselect",
            "inputType",
            "hasRules",
            "dataType",
            "code",
            "formatMask",
            "min",
            "max",
            "description"
          ].concat(input.customFields)
        )[0];
        obj.values = [];
        obj[productAttributes.inputType] = true;
        obj.attributeId = category.id.concat(
          "<" + productAttributes.attributeId
        );
        if (productAttributes && productAttributes.values) {
          productAttributes.values.forEach(record => {
            let attributeValueObj = objectMap(
              [record],
              ["id", "label", "value", "displaySequence"].concat(
                input.customFields
              )
            )[0];
            attributeValueObj = Object.assign(attributeValueObj, {
              attributeId: category.id.concat(
                "<" + productAttributes.attributeId
              ),
              defaultSelected: productAttributes.userValues === record.value,
              uniqueAttributeId: parentId + "" + productAttributes.attributeId
            });
            obj.values.push(attributeValueObj);
          });
        }
        attributeCategory.productAttributes.push(obj);
      });
    }
    if (input.customFields && input.customFields.length) {
      input.customFields.map(field => {
        if (category.hasOwnProperty(field)) {
          attributeCategory.customFields[field] = category[field];
        }
      });
    }
    return attributeCategory;
  }

  /**
   * Holds all the product attributes in a array
   */
  public productAttributes?: any[] = [];
  /**
   * Stores each product category code
   */
  public code: string;
  /**
   * Stores each product category name
   */
  public name: string;
  /**
   * Stores each product category unique Id
   */
  public id: number;

  /**
   * custom attributes for an offer
   */
  public customFields?: any = {};
}
