import { SalesForceNamespace } from "../../common";
import { objectMap } from "../../utils";
/**
 * Class that defines PriceAdjustment Details.
 */
export class PriceAdjustment {
  /**
   * This function parse the PriceAdjustment details to fetch all pricing information.
   * @param priceAdjustmentResponse
   * @return {PriceAdjustment}
   */
  public static parse(
    priceAdjustmentResponse: any,
    customFields: any[]
  ): PriceAdjustment {
    const priceAdjustment = new PriceAdjustment();
    if (priceAdjustmentResponse && priceAdjustmentResponse.records) {
      priceAdjustmentResponse.records.forEach(adjustment => {
        const adjustmentObj: any = {};
        let chargeType = "oneTimeCharge";
        let priceDetails =
          adjustment[SalesForceNamespace.namespace + "__OneTimeCharge__c"];
        if (!priceDetails) {
          chargeType = "recurringCharge";
          priceDetails =
            adjustment[SalesForceNamespace.namespace + "__RecurringCharge__c"];
        }

        adjustmentObj.id = adjustment.id;
        adjustmentObj.value = priceDetails.value;
        adjustmentObj.chargeType = chargeType;
        if (priceDetails) {
          adjustmentObj.priceDetails = [];
          priceDetails.pricedetail.forEach(priceRecord => {
            const fetchedPriceDetails = objectMap(
              [priceRecord],
              [
                "actions",
                "AdjustmentMethod#adjustmentMethod",
                "AdjustmentType#adjustmentType",
                "AdjustmentValue#adjustmentValue",
                "Description#description",
                "DetailType#detailType",
                "EndValue#endValue",
                "StartValue#startValue"
              ].concat(customFields)
            )[0];
            if (
              fetchedPriceDetails.adjustmentType === "None" &&
              fetchedPriceDetails.adjustmentValue === null
            ) {
              fetchedPriceDetails.adjustmentValue =
                fetchedPriceDetails.startValue;
            }
            adjustmentObj.priceDetails.push(fetchedPriceDetails);
          });
        }
        if (customFields && customFields.length) {
          customFields.map(field => {
            if (adjustment.hasOwnProperty(field)) {
              adjustmentObj[field] = adjustment[field];
            }
          });
        }
        priceAdjustment.records.push(adjustmentObj);
      });
    }
    return priceAdjustment;
  }

  /**
   * Stores all price adjustments in an array.
   */
  public records: any = [];
}
