import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { B2BInput } from "../common/B2BInput";
/**
 * Class that defines input to get catalogs.
 * @extends B2BInput
 */
export class GetCartProductsInput extends B2BInput {
  public fieldsToInclude = [
    "lastRecordId",
    "includeAttachment",
    "filters",
    "includeIneligible",
    "attributes",
    "includeAttributes",
    "query",
    "pagesize",
    "offsetSize",
    "hierarchy",
    "category",
    "fields"
  ];

  /**
   * Cart Id (Salesforce Id).Opportunity, Quote or Order Id. - Required
   */
  public cartId: string;

  /**
   * hierarchy depth returned for list of products
   */
  public hierarchy?: number = 0;

  /**
   * Offset from which to start reading products, for pagination.
   */
  public offsetSize?: number;

  /**
   * Number of records to be returned
   */
  public pageSize?: number;

  /**
   * The last record ID from the previous search result, if available
   */
  public lastRecordId?: string;

  /**
   * Filter field values.
   */
  public filters?: string;

  /**
   * Search text
   */
  public query?: string;

  /**
   * Attribute filters. For example, for the attribute memory (attributecode=attr1) with selected values 16gb and 32gb and attribute color (attr2) with red selected, the format will be as shown in the example.
   */
  public attributes?: string;

  /**
   * Fetch Products by Catalog Category
   */
  public category?: string;

  /**
   * Specifies whether to return a list of attributes and their values for the product.
   */
  public includeAttributes?: boolean;

  /**
   * Whether product attachments are returned
   */
  public includeAttachment?: boolean;

  /**
   * Remote class to handle get cart products.
   */
  public remoteClass: string = "CpqAppHandler";

  /**
   * Remote method in the boave Remote Class to handle get cart products.
   */
  public remoteMethod: string = "getCartsProducts";

  /**
   * List of fields to return in the response, separated by commas.
   */
  public fields?: string[];

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    if (!this.cartId && !this.actionObj) {
      throw new Error("GetCartProductsInput::getAPIPath() must have cartId.");
    }
    return (
      `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${
        this.cartId
      }/products` + this.getEncodedQueryString()
    );
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(cartItems?: any): any {
    return {};
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};
    this.fieldsToInclude.forEach(eachField => {
      if (this[eachField]) {
        inputMap[eachField] = this[eachField];
      }
    });
    if (this.fields) {
      inputMap.fields = this.fields.join(",");
    }
    return inputMap;
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      this.fieldsToInclude
    );

    return encodedQueryString;
  }
}
