import { BaseDataSourceInput } from "../common";

export class DataRaptorInput extends BaseDataSourceInput {
  /**
   * Dataraptor bundle name
   */

  public bundleName: string;
  /**
   * input params as a string - format: "name=vlocity&state=ca"
   */
  public inputParamsString?: string;
  /**
   * input params as an object- format: {"name": "vlocity", "state":"ca"}
   */
  public inputMap?: object;
  constructor(dataRaptorInput) {
    super(
      dataRaptorInput.cacheable,
      dataRaptorInput.cacheExpiration,
      dataRaptorInput.retryCount,
      dataRaptorInput.timeoutInMillis,
      dataRaptorInput.reload,
      dataRaptorInput.cacheType
    );
    this.bundleName = dataRaptorInput.bundleName;
    this.inputParamsString = dataRaptorInput.inputParamsString;
    this.inputMap = dataRaptorInput.inputMap;
  }
  public digest(): Promise<string> {
    return null;
  }
}
