import { queryStringEncode } from "../../utils";
import { B2BInput } from "../common/B2BInput";
/**
 * Class that defines input to update lineItem.
 * @extends B2BInput
 */
export class ItemsAttributeInput extends B2BInput {
  /**
   * cartId as part of the POST body of the API for updating cartItems Attribute.
   * cartId - the cart Id from which lineitems attributes needs to be updated.
   */
  public cartId: string;

  /**
   * id as part of the POST body of the API for updating lineItems attribute.
   * id - the offer Id which needs to be updated to the cart.
   */
  public id: string;

  /**
   * itemId as part of the POST body of the API for updating lineItems attribute.
   * itemId - the offer Id which needs to be updated to the cart.
   */
  public itemId: string;

  /**
   * Line item inside the anonymous cart to be updated.
   */
  public items: any;

  /**
   * Indicator if user intends to enable rapid attribute to the anonymous shopping cart.
   */
  public isRapidAttributeEnabled: boolean = false;

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    return "";
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(cartItems?: any): any {
    if (!this.actionObj) {
      throw new Error("ItemsAttributeInput must have actionObj.");
    }
    if (!this.cartId) {
      throw new Error("ItemsAttributeInput must have cartId.");
    }
    if (!this.id) {
      throw new Error("ItemsAttributeInput must have id.");
    }
    if (!this.itemId) {
      throw new Error("ItemsAttributeInput must have itemId.");
    }
    if (!this.items) {
      throw new Error("ItemsAttributeInput must have items.");
    }
    const currentCartItem = cartItems.records.find(
      cartItem => cartItem.Id.value === this.itemId
    );
    return this.actionObj;
  }

  /**
   * Function that constructs the input map object containing all query parameters for API.
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};
    return inputMap;
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
