export class CrossActionsService {
  public static foundProductGroups = [];

  public static hasLineItemAlreadyBeenAddedToParent(
    parentInCardData,
    toBeAddedLineItem
  ) {
    let toBeAddedLineItemHasBeenAdded = false;
    let numOfLineItemsUnderParent;
    if (!parentInCardData.lineItems) {
      return toBeAddedLineItemHasBeenAdded;
    }
    numOfLineItemsUnderParent = parentInCardData.lineItems.records.length;
    for (let i = 0; i < numOfLineItemsUnderParent; i++) {
      if (
        parentInCardData.lineItems.records[i].Id.value ===
        toBeAddedLineItem.Id.value
      ) {
        toBeAddedLineItemHasBeenAdded = true;
        break;
      }
    }
    return toBeAddedLineItemHasBeenAdded;
  }

  public static findProductGroupsWithLineItemsAmongNodeList(searchNodeList) {
    let foundProductGroup;

    this.foundProductGroups = [];
    for (const searchNode of searchNodeList) {
      foundProductGroup = this.findProductGroupWithLineItemsInsideNode(
        searchNode
      );
      if (foundProductGroup) {
        this.foundProductGroups.push(foundProductGroup);
      }
    }

    return this.foundProductGroups;
  }

  public static findProductGroupWithLineItemsInsideNode(searchNode) {
    let foundProductGroup = [];

    if (
      searchNode.lineItems &&
      searchNode.lineItems.records &&
      searchNode.lineItems.records.length > 0
    ) {
      return searchNode;
    } else if (
      searchNode.productGroups &&
      searchNode.productGroups.records &&
      searchNode.productGroups.records.length > 0
    ) {
      for (const record of searchNode.productGroups.records) {
        foundProductGroup = this.findProductGroupWithLineItemsInsideNode(
          record
        );
        if (foundProductGroup) {
          this.foundProductGroups.push(foundProductGroup);
        }
      }
    }
    return null;
  }

  public static findProductGroupAmongNodeList(
    searchId,
    searchProductHierarchyPath,
    searchNodeList
  ) {
    let foundProductGroup;

    for (const searchNode of searchNodeList) {
      foundProductGroup = this.findProductGroupInsideNode(
        searchId,
        searchProductHierarchyPath,
        searchNode
      );
      if (foundProductGroup) {
        break;
      }
    }
    return foundProductGroup || null;
  }

  public static findProductGroupInsideNode(
    searchId,
    searchProductHierarchyPath,
    searchNode
  ) {
    let foundProductGroup;

    // A product group has Id.value as product2Id, instead of lineItemId. Since that is not unique,
    // we also need to compare productHierarchyPath
    if (
      searchNode.Id.value === searchId &&
      searchNode.productHierarchyPath === searchProductHierarchyPath
    ) {
      return searchNode;
    } else {
      if (
        searchNode.productGroups &&
        searchNode.productGroups.records &&
        searchNode.productGroups.records.length > 0
      ) {
        for (const record of searchNode.productGroups.records) {
          foundProductGroup = this.findProductGroupInsideNode(
            searchId,
            searchProductHierarchyPath,
            record
          );
          if (foundProductGroup) {
            return foundProductGroup;
          }
        }
      } else if (
        searchNode.lineItems &&
        searchNode.lineItems.records &&
        searchNode.lineItems.records.length > 0
      ) {
        for (const record of searchNode.lineItems.records) {
          foundProductGroup = this.findProductGroupInsideNode(
            searchId,
            searchProductHierarchyPath,
            record
          );
          if (foundProductGroup) {
            return foundProductGroup;
          }
        }
      } else {
        return null;
      }
    }
  }

  public static findChildItemInProductGroups(record) {
    return this.findItemsInProductGroups(record, "childProducts");
  }

  public static findLineItemInProductGroups(record) {
    return this.findItemsInProductGroups(record, "lineItems");
  }

  public static findItemsInProductGroups(record, param: string) {
    let productItems;

    if (record[param]) {
      return record[param].records;
    } else if (record.productGroups) {
      productItems = this.findItemsInProductGroups(
        record.productGroups.records[0],
        param
      );
    }

    return productItems;
  }

  public static removeLineItemFromParent(
    parentInCardData,
    toBeRemovedLineItemId
  ) {
    let lineItemList;
    const lineItemListWithoutTheRemovedItem = [];

    if (parentInCardData.lineItems) {
      lineItemList = JSON.parse(
        JSON.stringify(parentInCardData.lineItems.records)
      );
      for (const currentLineItem of lineItemList) {
        if (currentLineItem.Id.value !== toBeRemovedLineItemId) {
          lineItemListWithoutTheRemovedItem.push(currentLineItem);
        }
      }

      // Delete the lineItem from parent by replacing the existing lineItems under the parent
      // by an array without the to-be-deleted lineItem
      parentInCardData.lineItems.records = lineItemListWithoutTheRemovedItem;
    }
    return parentInCardData;
  }

  /**
   * This function will search an productGroup/LineItem with in given bundle
   * @param product
   * @param idToBeSearched
   * @return {any}
   */
    public static searchLineItem(product, idToBeSearched) {
      // base check the required object
      if (product.id && product.id === idToBeSearched) {
        return product;
      }
      if(product.Id && product.Id.value === idToBeSearched){
        return product;
      }
      if (product.productGroups) {
        for (let i = 0; i < product.productGroups.length; i++) {
          const foundProductGroup = this.searchLineItem(
            product.productGroups[i],
            idToBeSearched
          );
          if (foundProductGroup) {
            // If the product group was found in the recursive call, bubble it up.
            return foundProductGroup;
          }
        }
      }
      if (product.lineItems && product.lineItems.records) {
        for (let i = 0; i < product.lineItems.records.length; i++) {
          const foundLineItem = this.searchLineItem(
            product.lineItems.records[i],
            idToBeSearched
          );
          if (foundLineItem) {
            // If the line item was found in the recursive call, bubble it up.
            return  foundLineItem;
          }
        }
      }
    }
  }
