import { queryStringEncode } from "../../utils";
import { B2BInput } from "../common/B2BInput";
/**
 * Class that defines input to add product to the cart.
 * @extends B2BInput
 */
export class AddProductToCartInput extends B2BInput {
  /**
   * cartId as part of the POST body of the API for adding product to the working cart.
   * Optional when adding child product to the parent.
   */
  public cartId?: string;

  /**
   * childProductId as part of the POST body of the API for adding product to the working cart.
   * childProductId - the offer Id which needs to be added to the cart.
   */
  public childProductId: string;

  /**
   * parentId as part of the POST body of the API for adding product to the working cart.
   */
  public parentId?: string;

  /**
   * rootBundleId as part of the POST body of the API for adding product to the working cart.
   */
  public rootBundleId: string;

  /**
   * The parent object of addon to be added to the cart.
   */
  public parentObject?: { [key: string]: any };

  /**
   * Remote method in the above Remote Class to handle add cart items.
   */
  public remoteMethod: string = "postCartsItems";

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    return "";
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(cartItems?: any): any {
    if (!this.actionObj && !this.childProductId) {
      throw new Error("AddProductToCartInput must have childProductId.");
    }
    if (!this.actionObj && !this.cartId) {
      throw new Error("AddProductToCartInput must have cartId.");
    }

    if (!this.actionObj) {
      this.actionObj = this.prepareAddToCartActionObject();
    } else if (this.rootBundleId) {
      const currentCartItem = cartItems.records.find(
        cartItem => cartItem.Id.value === this.rootBundleId
      );
      this.parentObject = this.findLineItem(
        this.cloneDeep(currentCartItem),
        this.parentId
      );
    }
    return this.actionObj;
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};
    return inputMap;
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  public prepareAddToCartActionObject() {
    const actionObj = {
      client: {
        params: {}
      },
      link:
        "/services/apexrest/vlocity_cmt/v2/cpq/carts/" + this.cartId + "/items",
      remote: {
        methodName: "postCartsItems",
        params: {}
      },
      rest: {
        method: "POST",
        link:
          "/services/apexrest/vlocity_cmt/v2/cpq/carts/" +
          this.cartId +
          "/items",
        params: {
          cartId: this.cartId,
          hierarchy: -1,
          includeAttachment: false,
          items: [{ itemId: this.childProductId }],
          lastRecordId: null,
          methodName: "postCartsItems",
          pagesize: 10,
          price: true,
          query: null,
          validate: true
        }
      }
    };
    return actionObj;
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
