import DataSourceBrowserDB from "../../common/DataSourceBrowserDB";
import { Logger } from "../../logger";
import { BaseDataSourceInput } from "../common";
import IDataSource from "../DataSource.interface";
import Delay from "../utils/delay";
import { isNode } from "../utils/isNode";
import { SoslInput } from "./";

const requestError: string = "Request timed out";

export class SoslDataSource implements IDataSource {
  // @TODO: Avoid using any
  private connection: any;
  private input: SoslInput;
  private dataSourceDB: DataSourceBrowserDB;
  private inPlatformData?: Promise<Object>;
  constructor(
    connection: any,
    input: SoslInput,
    dataSourceDB: DataSourceBrowserDB,
    inPlatformData?: Promise<Object>
  ) {
    this.connection = connection;
    this.input = input;
    this.input.retryCount =
      this.input.retryCount !== undefined
        ? this.input.retryCount
        : Delay.defaultRetryCount;
    this.input.timeoutInMillis =
      this.input.timeoutInMillis !== undefined
        ? this.input.timeoutInMillis
        : Delay.defaultTimeout;
    this.dataSourceDB = dataSourceDB;
    this.inPlatformData = inPlatformData;
  }

  /**
   * Executes the SoslDataSource
   * Reserved characters in SOSL search text are escaped if the input query starts with `FIND` followed by any number of spaces and opening curly brace `{`
   * @returns Promise
   */

  public async execute(): Promise<any> {
    Logger.log("SOSL", this.input);
    try {
      if (typeof this.inPlatformData === "function") {
        const dataHandler: any = this.inPlatformData;
        const soslData: any = {
          type: "search",
          value: JSON.stringify(this.input.query)
        };
        const response = await dataHandler(JSON.stringify(soslData));
        return response;
      }
      if (this.inPlatformData) {
        return this.inPlatformData;
      } else if (!isNode && this.input instanceof BaseDataSourceInput) {
        const iDBKey = await this.input.digest();
        return this.dataSourceDB
          .getDataFromCache(iDBKey, this.input.reload, this.input.cacheType)
          .catch(() => {
            return this.jsforceSosl(this.input)
              .then(async result => {
                (await this.dataSourceDB.getCacheableValue(
                  this.input.cacheable,
                  this.connection.instanceUrl
                )) &&
                  (await this.dataSourceDB.setCache(
                    iDBKey,
                    result,
                    this.input.cacheExpiration,
                    this.input.cacheType
                  ));
                return result;
              })
              .catch(error => {
                return error;
              });
          });
      } else {
        return this.jsforceSosl(this.input);
      }
    } catch (error) {
      let errMsg =
        typeof error === "object" && error !== null
          ? JSON.stringify(error)
          : error;
      return Promise.reject(new Error(errMsg));
    }
  }

  /**
   * get sosl
   * @param  {SoslInput} input
   * @returns Promise
   */
  private jsforceSosl(input: SoslInput): Promise<any> {
    return new Promise((resolve, reject) => {
      let timeout = setTimeout(() => {
        reject(new Error(requestError));
      }, this.input.timeoutInMillis);
      this.connection.search(
        this.escapeSOSLSearchCharacters(input.query),
        (error, response) => {
          clearTimeout(timeout);
          if (error) {
            if (this.input.retryCount === 0) {
              Logger.log("SOSL query failed", error);
              reject(error);
            } else {
              --this.input.retryCount;
              return Delay.addRetryDelay().then(() => {
                return this.jsforceSosl(input)
                  .then(resolve)
                  .catch(reject);
              });
            }
          }

          resolve(response);
        }
      );
    }).catch(error => {
      if (error.message === requestError && this.input.retryCount > 0) {
        --this.input.retryCount;
        return Delay.addRetryDelay().then(() => {
          return this.jsforceSosl(input);
        });
      }
    });
  }

  /**
   * This function finds the search text (text within curly braces) in the SOSL query and escapes the reserved keywords as per https://developer.salesforce.com/docs/atlas.en-us.soql_sosl.meta/soql_sosl/sforce_api_calls_sosl_find.htm
   * Replacement happens only if the query starts with `FIND` followed by any number of spaces and opening curly brace `{`
   * @param query SOSL Query
   */
  private escapeSOSLSearchCharacters(query: string): string {
    query = query.trim();
    if (query.search(/FIND\s*{/i) === 0) {
      const searchTextMatches = query.match(/{(.|[\r\n\s])+}/i);
      if (searchTextMatches && searchTextMatches.length >= 0) {
        // Regex matches the curly braces also, so extracting the text within curly braces.
        const searchText = searchTextMatches[0].substr(
          1,
          searchTextMatches[0].length - 2
        );
        const escaped = searchText.replace(
          /[-[\]&{}!()*:"'~\+\-\?.,\\^$|]/g,
          "\\$&"
        );
        return query.replace(/FIND\s*{(.|\s)*}/i, `FIND {${escaped}}`);
      } else {
        return query;
      }
    } else {
      return query;
    }
  }
}
