import { SalesForceNamespace } from "../../common";
import { CartItems } from "../cart";
import { CrossActionsService } from "./CrossActionsService";

export class ItemUpdated {
  public static handleCrossAction(data, parent, rootBundle): any {
    const cartitemObj = new CartItems();
    const inCartQtyField =
      SalesForceNamespace.namespace + "__InCartQuantityMap__c";
    let toBeUpdatedLineItem;
    let itemInCardData;
    let parentInCardData;
    let rootBundleInCartData;
    let productGroupParentInCardData;
    let productGroupParentFromAPIList;

    if (rootBundle) {
      // If changes are in root bundle
      if (data.records.Id.value === rootBundle.Id.value) {
        parentInCardData = rootBundle; // parent in card
        rootBundle.attributeCategories = data.records.attributeCategories;
      } else {
        parentInCardData = CartItems.findAndReplaceLineItem(
          rootBundle,
          data.records.Id.value
        ); // parent in card
      }

      if (
        data.records.lineItems &&
        data.records.lineItems.records &&
        data.records.lineItems.records.length > 0
      ) {
        // todo level base approach check
        parentInCardData[inCartQtyField] = data.records[inCartQtyField];

        // Handle multiple auto add records under a lineitems parent
        data.records.lineItems.records.forEach(record => {
          if (!record.isVirtualItem) {
            toBeUpdatedLineItem = record; // child
            itemInCardData = CartItems.findAndReplaceLineItem(
              parentInCardData,
              record.Id.value
            );
            if (itemInCardData) {
              toBeUpdatedLineItem.lineItems = itemInCardData.lineItems;
              toBeUpdatedLineItem.productGroups = itemInCardData.productGroups;
            }
            parentInCardData = cartitemObj.insertLineItemToParent(
              parentInCardData,
              toBeUpdatedLineItem
            );
          }
        });
      }

      if (
        data.records.productGroups &&
        data.records.productGroups.records &&
        data.records.productGroups.records.length > 0
      ) {
        // rootBundle in card
        rootBundleInCartData = rootBundle;

        // find the productGroup parents from API.  auto-add rule (data,actions) will only return the parent productGroups with lineItems
        productGroupParentFromAPIList = CrossActionsService.findProductGroupsWithLineItemsAmongNodeList(
          data.records.productGroups.records
        );

        // there could be lineItems to be added that belong to more than one productGroup, that's why we need to process each
        productGroupParentFromAPIList.forEach(productGroupParentFromAPI => {
          // find the corresponding productGroup parent in card, starting from the rootBundle in card
          productGroupParentInCardData = CrossActionsService.findProductGroupAmongNodeList(
            productGroupParentFromAPI.Id.value,
            productGroupParentFromAPI.productHierarchyPath,
            parentInCardData.productGroups.records
          );

          // todo level base approach check
          productGroupParentInCardData[inCartQtyField] =
            productGroupParentFromAPI[inCartQtyField];

          // Handle multiple auto add records under a productGroup parent
          productGroupParentFromAPI.lineItems.records.forEach(record => {
            if (!record.isVirtualItem) {
              toBeUpdatedLineItem = record; // child
              itemInCardData = CartItems.findAndReplaceLineItem(
                productGroupParentInCardData,
                record.Id.value
              );
              if (itemInCardData) {
                toBeUpdatedLineItem.lineItems = itemInCardData.lineItems;
                toBeUpdatedLineItem.productGroups =
                  itemInCardData.productGroups;
              }
              productGroupParentInCardData = cartitemObj.insertLineItemToParent(
                productGroupParentInCardData,
                toBeUpdatedLineItem
              );
            }
          });
        });
      }
    }
    return rootBundle;
  }
}
